<template>
  <div>
    <div class="top_ad">
      <van-icon
        name="arrow-left"
        size="24rem"
        color="#8c8989"
        @click="$router.back()"
      />
      <div class="arrow-left">买家秀详情</div>
    </div>
    <van-loading type="spinner" v-if="!detail" />
    <div class="buy_xiu">
      <div class="hot_entity">
        <div class="hot_left">
          <van-image :src="detail?.data?.entity_circle?.img" class="img_p">
            <template v-slot:loading>
              <van-loading type="spinner" size="20" />
            </template>
          </van-image>
          <div class="circle_name">{{ detail?.data?.entity_circle?.name }}</div>
        </div>
        <div class="hot_rigth">
          {{ detail?.data?.entity_circle?.hot }}热度
          <van-icon name="arrow" />
        </div>
      </div>
      <div class="user_t">
        <div class="user_top">
          <div class="user_left">
            <div class="img_p">
              <img :src="detail?.data?.icon" alt="" />
            </div>
            <div class="n_t">
              <div class="name_u">{{ detail?.data?.nickname }}</div>
              <div class="create_time">{{ detail?.data?.create_time }}</div>
            </div>
          </div>
          <div class="user_rigth" 
            v-if="guangZhu.includes(detail?.data?.id) ? false : true"
          @click="addSju(detail?.data?.id)">关注</div>
            <div
          class="yiguangz"
          v-if="guangZhu.includes(detail?.data?.id) ? true : false"
           @click="addSju(detail?.data?.id)"
        >
          已关注
        </div>
        </div>
        <div class="content">
          <div class="con_title">{{ detail?.data?.content }}</div>
          <div class="con_img">
            <div
              v-for="(img, index) in detail?.data?.pic_list"
              :key="index"
              class="imag"
              @click.stop="viewimg(index, detail?.data?.pic_list)"
            >
              <van-image
                :src="img.img"
                fit="cover"
                height="100%"
                width="100%"
                class="img"
              >
                <template v-slot:loading>
                  <van-loading type="spinner" size="20" />
                </template>
              </van-image>
            </div>
          </div>
          <div class="commen_text">共{{detail?.data?.comment_num ? detail?.data?.comment_num : '0'}}条评论</div>
          <van-list
            v-model="loading"
            :finished="finished"
            finished-text="没有更多了"
            @load="onLoad"
          >
            <van-cell v-for="(item,index) in dataDetail" :key="index" >
              <div class="avatar" >
          <div class="img_pic">
            <van-image :src="item.img ? item.img : ''" round fit="cover" >
              <template v-slot:loading>
                <van-loading type="spinner" size="20" />
              </template>
            </van-image>
          </div>
          <div class="fl">
            <div class="nickname_top">
              <div class="nickname">{{ item.nickname }}</div>
              <div class="create_time">{{ item.create_time }}</div>
            </div>
            <div class="answer_content">{{ item.comment }}</div>
          </div>
          <div class="good-job">
             <van-icon name="good-job-o" size="18rem" :color="goodjob.includes(item.id)?'red':''" 
                          @click="min_dianzan(item.id);"/> <div class="goodjob" v-text="goodjob.includes(item.id)?item.praise_num*1+1:item.praise_num"></div>
          </div>
        </div>
            </van-cell>
          </van-list>
        </div>
      </div>
    </div>
    <div class="footer">
      <div
        class="buy_p"
        @click="
          $router.push({
            path: '/showgoods',
            query: { id: detail?.data?.products[0]?.product_id },
          })
        "
      >
        ￥{{ detail?.data?.products[0]?.price }}起 立即购买<van-icon
          name="arrow"
        />
      </div>
      <img src="@/assets/share.png" alt="" />
      <div>
        <img src="@/assets/msgf.png" alt="" /><span
          v-text="detail?.data?.comment_num ? detail?.data?.comment_num : ''"
        ></span>
      </div>
      <div>
        <van-icon
          name="good-job-o"
          size="18rem"
          :color="arrdianzs.includes(detail?.data?.id) ? 'red' : ''"
          @click="dianzans(detail?.data?.id)"
        />
        <span
          v-text="
            arrdianzs.includes(detail?.data?.praise_num)
              ? detail?.data?.praise_num * 1 + 1
              : detail?.data?.praise_num
          "
        >
        </span>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import { ImagePreview } from "vant";
Vue.use(ImagePreview);
export default {
  data: function () {
    return {
      detail: [],
      arrdianzs: JSON.parse(localStorage.getItem("arrdianzs")) || [],
      dataDetail: [],
      loading: false,
      finished: false,
      after: 0,
        guangZhu: JSON.parse(localStorage.getItem("guangZhu")) || [],
       goodjob:JSON.parse(localStorage.getItem("goodjob"))|| []
    };
  },
  created() {
    this.getDetail(this.$route.query.id);
  },
  beforeRouteUpdate(to) {
    this.getDetail(to.query.id);
  },
  methods: {
       addSju:function (event) {
      // console.log(event);
      if (this.guangZhu.includes(event)) {
        this.guangZhu = this.guangZhu.filter((o) => {
          return !(o == event);
        });
      } else {
        this.guangZhu = [...this.guangZhu, event];
      }
      localStorage.setItem("guangZhu", JSON.stringify(this.guangZhu));
    },
     min_dianzan:function (event) {
         if (this.goodjob.includes(event)) {
      this.goodjob=this.goodjob.filter((o)=>{
        return !(o==event)
      })
    }else{
      this.goodjob=[...this.goodjob,event]
    }
    // console.log(this.goodjob ,event);
    localStorage.setItem("goodjob",JSON.stringify(this.goodjob))
      
    },
    onLoad() {
      // 异步更新数据
      // setTimeout 仅做示例，真实场景中一般为 ajax 请求

      this.axios
        .get("https://apis.netstart.cn/xmsc/communicate/mizone_comment_list?", {
          params: {
            comment_type: "product_comment",
            product_comment_id: this.$route.query.id,
            page_size: 10,
            page_index: this.after + 1,
          },
        })
        .then((res) => {
          this.dataDetail =[...this.dataDetail, ... res.data.data.comment_list];
          this.$nextTick(() => {
            this.loading = false;
            if (this.dataDetail.length >= 40) {
              this.finished = true;
            }
            this.after += 1;
          });
          // console.log(res);
        });
    },
    dianzans: function (event) {
      console.log(event);
      if (this.arrdianzs.includes(event)) {
        this.arrdianzs = this.arrdianzs.filter((o) => {
          return !(o == event);
        });
      } else {
        this.arrdianzs = [...this.arrdianzs, event];
      }
      localStorage.setItem("arrdianzs", JSON.stringify(this.arrdianzs));
    },
    viewimg(i, e) {
      let imgs = [];

      e.forEach((o) => {
        imgs.push(o.img);
      });
      ImagePreview({
        images: imgs,
        showIndex: true,
        loop: false,
        startPosition: i,
      });
    },
    getDetail(id) {
      this.axios
        .get(
          "https://apis.netstart.cn/xmsc/communicate/mizone_product_comment_detail?",
          {
            params: { comment_id: id },
          }
        )
        .then((res) => {
          this.detail = res.data;
          // console.log(res.data);
        });
    },
  },
};
</script>

<style lang="less" scoped>
.user_t{
.content{
.van-list{
  .van-cell{
    padding: 0;
  }
}
}
}

