   border-bottom: 1rem solid lightgrey;
    .img_pic {
       border-radius: 50%;
       padding-top: 10rem;
display: flex;

 .van-image{
     width: 30rem;
      height: 30rem;

 }
   
    }

    .fl {
   width: 100%;
