<template>
  <section class="sy">
     <dir class="loadingg">
      <van-loading type="spinner" v-if="!homeTJ" size="46" color="red"/>
    </dir>
   <div v-if="homeTJ">
      <div class="tupian_top"   v-if="showTop" >
      <img src="@/assets/top.png" @click="sllTop" id="btnBox" alt="">
     </div>
     <HomeSwipe :homeTJ="homeTJ" />
    <MiJing :homeTJ="homeTJ?.data?.sections[1].body.items" />
