    <div style="height: 8rem; background-color: rgb(245, 245, 245)"></div>
    <BigPic :homeTJ="homeTJ?.data?.sections[6].body.items[0]" />
    <ListTwoLi :homeTJ="homeTJ?.data?.sections[8].body.items" />
    <ListTwoLi :homeTJ="homeTJ?.data?.sections[10].body.items" />
    <ListTwoLi :homeTJ="homeTJ?.data?.sections[12].body.items" />
    <ListTwoLi :homeTJ="homeTJ?.data?.sections[14].body.items" />
    <hr />
    <ActiveTitle :homeTJ="homeTJ?.data?.sections[16].body.items[0]" />
    <div style="height: 8rem; background-color: rgb(245, 245, 245)"></div>
    <BigPic :homeTJ="homeTJ?.data?.sections[18].body.items[0]" />
    <ActiveTitle :homeTJ="homeTJ?.data?.sections[20].body.items[0]" />
    <div style="height: 8rem; background-color: rgb(245, 245, 245)"></div>
    <BigPic :homeTJ="homeTJ?.data?.sections[22].body.items[0]" />
