    <ListTwoLi :homeTJ="homeTJ?.data?.sections[26].body.items" />
    <ActiveTitle :homeTJ="homeTJ?.data?.sections[28].body.items[0]" />
    <div style="height: 8rem; background-color: rgb(245, 245, 245)"></div>
    <BigPic :homeTJ="homeTJ?.data?.sections[30].body.items[0]" />
    <ActiveTitle :homeTJ="homeTJ?.data?.sections[31].body.items[0]" />
    <div style="height: 8rem; background-color: rgb(245, 245, 245)"></div>
    <ActiveTitle :homeTJ="homeTJ?.data?.sections[33].body.items[0]" />
    <div style="height: 8rem; background-color: rgb(245, 245, 245)"></div>
    <BigTwo :homeTJ="homeTJ?.data?.sections[35].body.items" />
    <BigTwo :homeTJ="homeTJ?.data?.sections[37].body.items" />
