   </div>
   
  </section>
</template>
<script>
import HomeSwipe from "@/components/homeview/tool/HomeSwipe.vue";
import MiJing from "@/components/homeview/tool/MiJing.vue";
import KkXiao from "@/components/homeview/tool/KkXiao.vue";
import BigPic from "@/components/homeview/tool/BigPic.vue";
import ListTwoLi from "@/components/homeview/tool/ListTwoLi.vue";
import ActiveTitle from "@/components/homeview/tool/ActiveTitle.vue";
import BigTwo from "@/components/homeview/tool/BigTwo.vue";
export default {
  props: {
    homeTJ: Object,
  },
  data:function () {
    return{
       showTop:false,
    }
  },
  components: {
    HomeSwipe,
    MiJing,
    KkXiao,
    BigPic,
    ListTwoLi,
    ActiveTitle,
    BigTwo,
  },
   mounted(){
  window.addEventListener("scroll",this.scrollToTop)
  },
    destroyed(){
  window.removeEventListener("scroll",this.scrollToTop)
  },
  methods: {
    sllTop:function () {
      document.documentElement.scrollTop=0
    },
  scrollToTop:function () {
    // let topBtn=document.getElementById("btnBox")
    var scrollTop=window.pageYOffset||document.documentElement.scrollTop||document.body.scrollTop;
    // console.log(scrollTop);
    if (scrollTop>500) {
      this.showTop=true
    }else if (scrollTop<500){
      this.showTop=false
    }
  },
  }
};
</script>

<style lang="less">
