<template>
  <div>
    <div class="top_ad">
      <van-icon
        name="arrow-left"
        size="24rem"
        color="#8c8989"
        @click="$router.back()"
      />
      <div class="arrow-left">问题详情</div>
    </div>

    <van-loading type="spinner" v-if="!detail" />
    <div class="product_img">
      <img :src="detail?.data?.product_img_url" alt="" />
      <div class="img_name">{{ detail?.data?.product_name }}</div>
    </div>
    <div class="linh"></div>
    <div class="q_title">
      <div class="q_title_big">
        {{ detail?.data?.question }}
      </div>
      <div class="user_top">
        <div class="user_left">
          <div class="img_p">
            <img :src="detail?.data?.avatar" alt="" />
          </div>
          <div class="n_t">
            <div class="name_u">{{ detail?.data?.nickname }}</div>
            <div class="create_time">提问于{{ detail?.data?.create_time }}</div>
          </div>
        </div>
        <div class="user_rigth">关注问题</div>
      </div>
    </div>
    <div class="linh"></div>
          <van-list
            v-model="loading"
            :finished="finished"
            finished-text="没有更多了"
            @load="onLoad"
          >
            <van-cell v-for="(item,index) in dataDetail" :key="index" >
              <div class="avatar" >
          <div class="img_pic">
            <van-image :src="item.avatar ? item.avatar : ''" round fit="cover" >
              <template v-slot:loading>
                <van-loading type="spinner" size="20" />
              </template>
            </van-image>
          </div>
          <div class="fl">
            <div class="nickname_top">
              <div class="nickname">{{ item.nickname }}</div>
              <div class="create_time">{{ item.create_time }}</div>
            </div>
            <div class="answer_content">{{ item.answer_content }}</div>
          </div>
          <div class="good-job">
             <van-icon name="good-job-o" size="18rem" :color="goodjob.includes(item.answer_id)?'red':''" 
                          @click="min_dianzan(item.answer_id);"/> <div class="goodjob" v-text="goodjob.includes(item.answer_id)?item.dislike_num*1+1:item.dislike_num"></div>
          </div>
        </div>
            </van-cell>
          </van-list>
    
  </div>
</template>

<script>
export default {
  data: function () {
    return {
      detail: [],
       dataDetail: [],
      loading: false,
      finished: false,
      after: 0,
       goodjob:JSON.parse(localStorage.getItem("goodjob"))|| []

    };
  },
  created() {
    this.getDetail(this.$route.query.id);
  },
  beforeRouteUpdate(to) {
    this.getDetail(to.query.id);
  },
  methods: {
    getDetail(id) {
      this.axios
        .get("https://apis.netstart.cn/xmsc/communicate/question_detail?", {
          params: { q_id: id, product_id: 0 },
        })
        .then((res) => {
          this.detail = res.data;
          // console.log(res.data);
        });
    },
     min_dianzan:function (event) {
         if (this.goodjob.includes(event)) {
      this.goodjob=this.goodjob.filter((o)=>{
        return !(o==event)
      })
    }else{
      this.goodjob=[...this.goodjob,event]
    }
    // console.log(this.goodjob ,event);
    localStorage.setItem("goodjob",JSON.stringify(this.goodjob))
      
    },
     onLoad() {
      // 异步更新数据
      // setTimeout 仅做示例，真实场景中一般为 ajax 请求

      this.axios
        .get("https://apis.netstart.cn/xmsc/communicate/question_answer_list?", {
          params: {
           q_id:this.$route.query.id,
           top_answer_id:"top_answer_id",
           size:10,
           after:this.after + 10,
           need_secondary:true,
           is_hot:false
          },
        })
        .then((res) => {
          // console.log(11,res);
          this.dataDetail =[...this.dataDetail, ... res.data.data.answer_list];
          this.$nextTick(() => {
            this.loading = false;
            if (this.dataDetail.length >= 30) {
              this.finished = true;
            }
            this.after += 1;
          });
          
        });
    },
  },
};
</script>

<style lang="less" scoped>
 .avatar {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    padding: 10rem 0;
   border-bottom: 1rem solid lightgrey;
    .img_pic {
       border-radius: 50%;
       padding-top: 10rem;
display: flex;

 .van-image{
     width: 30rem;
      height: 30rem;

 }
   
    }

    .fl {
   width: 100%;
      .nickname_top {
        color: rgb(172, 168, 168);
        display: flex;
        justify-content: left;
        align-items: center;
        .nickname {
          padding: 6rem 8rem;
        }
      }
      .answer_content {
        text-align: left;
        padding: 0 8rem;
      }
      
    }
    .good-job{
  display: flex;
    justify-content: right;
    align-items: flex-start;
    .goodjob{
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 13rem;
      padding-top: 2rem;

    }
    }
  }
.linh {
  height: 6rem;
  width: 100%;
  background-color: rgb(242, 242, 242);
  box-shadow: 0 0 5rem 2rem rgb(242, 242, 242);
}
.product_img {
  display: flex;
  justify-content: left;
  align-items: center;
  padding: 10rem 16rem;
  // position: fixed;
  img {
    width: 26rem;
  }
  .img_name {
    font-size: 14rem;
  }
}
.top_ad {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: sticky;
  padding: 16rem 10rem;
  height: 50rem;
  top: 0;
  z-index: 0;
  background-color: rgb(242, 242, 242);
  .arrow-left {
    width: 100%;
    font-size: 16rem;
    color: #666;
  }
}
.q_title {
  // position: relative;
  // top: 50rem;

  .q_title_big{
    text-align: left;
      padding: 16rem 16rem 10rem 16rem;
      font-size: 18rem;
      
  }
   .user_top {
    display: flex;
    align-items: center;
    justify-content: space-between;
     padding: 10rem 16rem 16rem 16rem;
    .user_left {
      display: flex;
      align-items: center;
      justify-content: left;
      .img_p {
        width: 30rem;
        height: 30rem;
        img {
          border-radius: 50%;
          width: 100%;
          height: 100%;
        }
      }
      .n_t {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: left;
        margin-left: 8rem;
        .name_u {
          text-align: left;
          font-weight: 500;
          color: #161616;
        }
        .create_time {
          font-weight: 500;
          color: rgba(0,0,0,.3);
          font-size: 12rem;
        }
      }
    }
    .user_rigth {
      display: flex;
      align-items: center;
      justify-content: right;
      padding: 6rem 12rem;
      background-color: rgba(255,89,52,.07);
      border-radius: 16rem;
      color: #ff5528;
      font-size: 13rem;
    }
  }
}
</style>