    </dir>
     <div class="tupian_top"   v-if="showTop" >
      <img src="@/assets/top.png" @click="sllTop" id="btnBox" alt="">
     </div>
    <div v-if="zhineng">
      <HomeSwipe :homeTJ="zhineng"  ref="myswipe" />
      <MiJing :homeTJ="zhineng?.data?.sections[2].body.items" />
      <MiJing :homeTJ="zhineng?.data?.sections[3].body.items" />
      <div style="height: 8rem; background-color: rgb(240, 241, 243)"></div>
      <BigPic :homeTJ="zhineng?.data?.sections[5].body.items[0]" />
