      <div style="height: 8rem; background-color: rgb(240, 241, 243)"></div>
      <ListThreeLi :homeTJ="zhineng?.data?.sections[9]?.body.items"/>
      <ListThreeLi :homeTJ="zhineng?.data?.sections[11]?.body.items"/>
      <BigPic :homeTJ="zhineng?.data?.sections[13].body.items[0]" />
      <div style="height: 8rem; background-color: rgb(240, 241, 243)"></div>
      <ListThreeLi :homeTJ="zhineng?.data?.sections[15]?.body.items"/>
      <div style="height: 8rem; background-color: rgb(240, 241, 243)"></div>
      <BigPic :homeTJ="zhineng?.data?.sections[17].body.items[0]" />
      <div style="height: 8rem; background-color: rgb(240, 241, 243)"></div>
      <BigPic :homeTJ="zhineng?.data?.sections[19].body.items[0]" />
      <div style="height: 8rem; background-color: rgb(240, 241, 243)"></div>
