  </section>
</template>

<script>
import HomeSwipe from "@/components/homeview/tool/HomeSwipe.vue";
import MiJing from "@/components/homeview/tool/MiJing.vue";
import BigPic from "@/components/homeview/tool/BigPic.vue";
import ListThreeLi from "@/components/homeview/tool/ListThreeLi.vue";
export default {
  data: function () {
    return {
      zhineng: null,
       showTop:false,
    };
  },
  components: {
    HomeSwipe,
    MiJing,
    BigPic,
    ListThreeLi,
  },
  created() {
    this.axios
      .get("zhineng.json")
      .then((res) => {
        this.zhineng = res.data.data;
        // setTimeout(()=>{
        // // this.$refs.myswipe.resize();
        // },500)
      });
  },
  watch:{
   
  },
   mounted(){
  window.addEventListener("scroll",this.scrollToTop)
  },
    destroyed(){
  window.removeEventListener("scroll",this.scrollToTop)
  },
  methods: {
    sllTop:function () {
      document.documentElement.scrollTop=0
    },
  scrollToTop:function () {
    // let topBtn=document.getElementById("btnBox")
    var scrollTop=window.pageYOffset||document.documentElement.scrollTop||document.body.scrollTop;
    // console.log(scrollTop);
    if (scrollTop>500) {
      this.showTop=true
    }else if (scrollTop<500){
      this.showTop=false
    }
  },
  }
};
</script>

<style lang="less" scoped>
 .loading {
position: absolute;
// top: 200rem;
margin-top: 200rem;
left: 35%;
  }
   .tupian_top{
  position: fixed;
  bottom: 70rem;
  right: 20rem;
  z-index: 99;
  img{
    width: 40rem;
  }
}
</style>