Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView,
    meta: { showNav: true },
  },
  {
    path: '/category',
    name: 'category',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/CategoryView.vue'),
    meta: { showNav: true },
  },
  {
    path:'/discover',
    name:'/discover',
    component:DiscoverView,
    meta: { showNav: true },
  },
  {
    path:'/cart',
    name:'/cart',
    component:CartView,
   
  },
  {
    path:'/user',
    component:UserView,
    meta: { showNav: true },
  },
  {
    path:'/search',
    component:SearchView,
    // children:[{
    //   path:"/list",
    //   component:ProductData,
    // }]
   
  },
  {
    path:"/search/list",
    component:ProductData,
  },
  {
    path:'/showgoods',
    component:ShowGoods
  },
  {
    path:'/actiongoods',
    component:ActionGoods
  },
  {
    path:'/activity',
    component:ActivityView
  },
  {
    path:'/allproduct',
    component:AllProduct,
    meta: { showNav: true },
  },
  {
    path:'/comment',
    name:'comment',
    component:ShowComment,
   
  },
  {
    path:'/address',
    name:'address',
    component:ShowAddress,
  },
{
  path:'/order',
  component:OrderList,
},
{
  path:'/checkout',
  component:OrderCheckout,
},
{
  path:'/addaddress',
  component:AddAddress,
},
{
  path:'/content',
  component:ContentPage,
},
{
  path:'/article',
  component:ArticlePage,
},
{
  path:'/vote',
  component:VotePage,
},
{
  path:'/question',
  component:QuestionPage,
},
{
  path:'/moment',
  component:MomentPage,
},
{
  path:'/product',
  component:ProductPage,
},
{
  path:'/goodspage',
  component:GoodsPage,
}

]

