<template>
  <div class="comment_tags_two" v-if="detail">
    <van-swipe :loop="false" :width="290" :show-indicators="false">
      <van-swipe-item v-for="(tag_list, index) in detail" :key="index">
        <div class="car_sw" @click="gettdata(tag_list)">
          <div class="user_avatar">
            <img :src="tag_list.user_avatar?tag_list.user_avatar:''" alt="" class="img_us" />
            <div class="user_er">
              <div class="user_name">
                {{ tag_list.user_name }}
              </div>
              <div class="add_time">
                {{ tag_list.add_time }}
              </div>
            </div>
          </div>
          <div class="comment_contents" v-html="tag_list.comment_content"></div>
          <div class="comment_images">
            <div
              class="img"
              v-for="(img, inde) in tag_list.comment_images"
              :key="inde"
              @click.stop="viewimg(inde,tag_list.comment_images)"
               overlayStyle="#000"
            >
              <van-image :src="img?img:''" fit="cover" width="100%">
                <template v-slot:loading>
                  <van-loading type="spinner" size="20" />
                </template>
              </van-image>
            </div>
          </div>
        </div>
      </van-swipe-item>
    </van-swipe>
  </div>
</template>

<script>
import Vue from 'vue';
import { ImagePreview } from 'vant';
Vue.use(ImagePreview);
export default {
  props: {
    detail: Array,
  },
  dtat:function () {
    return{
      
      index: 0,
    }
  },
  methods:{
    gettdata:function (tag_list) {
     this.$router.push({path:'comment'})
    //  console.log(tag_list);
       localStorage.setItem("getDetail",JSON.stringify(tag_list))
    },
    viewimg(i,e) {
      ImagePreview({
        images: e,
        showIndex: true,
        loop: false,
        startPosition: i
      })
    },
  }
};
</script>

<style lang="less">
.comment_tags_two {
  padding-bottom: 60rem;
  .van-tabs {
    .van-tabs__wrap {
      height: 170rem;
    }
  }
  .car_sw {
    width: 280rem;
    height: 170rem;
    border: 1rem solid rgb(211, 211, 211);
    padding: 10rem;
    border-radius: 8rem;
    .user_avatar {
      display: flex;
      align-items: center;
      justify-content: left;

      .img_us {
        width: 32rem;
        border-radius: 50%;
      }
      .user_er {
        display: flex;
        flex-direction: column;
        align-items: left;
        justify-content: center;
        margin-left: 12rem;
        .user_name {
          font-size: 10rem;
          text-align: left;
        }
        .add_time {
          font-size: 8sssssrem;
          text-align: left;
          color: #bbb6b6;
        }
      }
    }
    .comment_contents {
      overflow: hidden;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 2;
      text-align: left;
      text-overflow: ellipsis;
      margin: 6rem 0;
      height: 32rem;
      width: 100%;
    }
    .comment_images {
      display: flex;
      width: 280rem;
      .img {
        border-radius: 8rem;
        overflow: hidden;
        margin-right: 6rem;
        display: none;
        &:nth-child(1) {
          display: block;
        }
        &:nth-child(2) {
          display: block;
        }
        &:nth-child(3) {
          display: block;
        }
        img {
          width: 70rem;
          height: 70rem;
        }
      }
    }
  }
}
</style>