<template>
  <div>
    <div class="top_cart">
      <van-icon
        name="arrow-left"
        size="25rem"
        color="#777"
        @click="$router.push({ path: '/user' })"
      />
      <div
        style="font-size: 16rem; color: #666"
        v-html="`${arry[active] ? arry[active] : ''}订单`"
      ></div>
      <van-icon
        name="search"
        size="25rem"
        @click="$router.push({ path: '/search' })"
      />
      <!-- <img
        src="@/assets/ipt-icon.png"
        alt=""
        @click="$router.push({ path: '/search' })"
      /> -->
    </div>
    <section class="tree_top">
      <van-tabs
        animated
        @change="changeindex(active)"
        v-model="active"
        title-active-color="#ff6700"
        color="#ff6700"
        line-height="1rem"
        line-width="40rem"
      >
        <van-tab v-for="(tr, index) in tree" :title="tr" :key="index">
          <div class="empty_pic" v-if="!(buylist && active == 2)">
            <img src="@/assets/empty.png" alt="" />
            <div
              class="dingdan"
              v-html="`您还没有 ${tree[coun] ? tree[coun] : ''} 订单`"
            ></div>
          </div>
          <section class="buy_box" v-if="buylist && active == 2">
            <div class="buylist" v-for="(item, index) in buylist" :key="index">
              <div class="buy_fl">
                <img
                  :src="item.goods.img_url ? item.goods.img_url : ''"
                  alt=""
                />
                <div class="buy_name">{{ item.goods.name }}</div>
              </div>
              <div>
                {{ item.coun }} × <span>{{ item.goods.price }}</span>
              </div>
            </div>
          </section>
              <dir class="loading">
      <van-loading type="spinner" v-if="!orderlist" size="46" color="red" />
    </dir>
    <div class="orderlist" v-if="orderlist">
      <header>
        <img
          :src="
            orderlist?.header?.body?.items[0]?.img_url
              ? orderlist?.header?.body?.items[0]?.img_url
              : ''
          "
          alt=""
        />
      </header>
      <ul class="recom_list">
        <li
          v-for="(li, index) in orderlist?.recom_list"
          :key="index"
          @click="
            $router.push({ path: 'showgoods', query: { id: li.action.path } })
          "
        >
          <van-image :src="li.image_url ? li.image_url : ''">
            <template v-slot:loading>
              <van-loading type="spinner" size="20" />
            </template>
          </van-image>
          <!-- <img :src="li.image_url" alt=""> -->
          <div class="link">
            <p>{{ li.name }}</p>
            <div>
              ￥{{ li.price }}
              <span v-if="li.price != li.market_price"
                >￥{{ li.market_price }}</span
              >
            </div>
          </div>
        </li>
      </ul>
    </div>
        </van-tab>
      </van-tabs>
    </section>


  </div>
</template>

<script>
export default {
  data: function () {
    return {
      orderlist: null,
      tree: ["全部", "待付款", "待收货"],
      coun: null,
      active: 0,
      arry: ["我的", "待支付", "待收货", "商品"],
      buylist: JSON.parse(localStorage.getItem("buyed")) || null,
    };
  },
  watch:{
  },
  methods: {
    changeindex: function (i) {
      this.active = i;
      if (!(i == 0)) {
        this.coun = i;
        // console.log(i);
      } else {
        this.coun = "";
      }
    },
  },
  //  beforeRouteUpdate(to) {
  //     this.active=to.query.page;
  // },
  created() {
    
    this.axios.get("https://apis.netstart.cn/xmsc/cart_recom").then((res) => {
      this.orderlist = res.data.data;
      //   console.log(res.data.data);
    });
    if (this.buylist) {
      this.active = 2;
      // console.log(this.buylist);
    }
     this.active=this.$route.query.page*1;
    //  console.log(this.$route.query.page,this.active);
  },
};
</script>

<style lang="less" scoped>
.buy_box {
  margin: 0 20rem;
  position: relative;
  z-index: 999;
  .buylist {
    display: flex;
    justify-content: space-between;
    align-items: center;

    border-bottom: 1rem solid lightgrey;
    .buy_fl {
      display: flex;
      justify-content: space-between;
      align-items: center;
      .buy_name {
        overflow: hidden;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 2;
        text-align: left;
        width: 200rem;
        text-overflow: ellipsis;
      }
      img {
        width: 70rem;
      }
    }
    font-size: 14rem;
    span {
      color: #000;
      font-weight: bold;
      font-size: 14rem;
    }
  }
}
.tree_top {
  position: relative;
  width: 100%;
  top: 50rem;
  z-index: 9;
}
.empty_pic {
  padding-top: 40rem;
  padding-bottom: 60rem;
  position: relative;
  // top: 60rem;
  .dingdan {
    color: #999;
    font-size: 16rem;

    padding-top: 20rem;
  }
  img {
    width: 100rem;
  }
}
.top_cart {
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 14rem 10rem;
  background: #f2f2f2;
  z-index: 9999;
  width: 100%;
  img {
    width: 30rem;
  }
}
.orderlist {
  width: 100%;

  header {
    img {
      width: 100%;
    }
  }
  .recom_list {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    li {
      width: 49.5%;
      img {
        width: 100%;
      }
      .link {
        margin: 10rem 20rem;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: left;
        text-align: left;
        p {
          font-size: 15rem;
          width: 160rem;
          text-overflow: ellipsis;
          overflow: hidden;
          white-space: nowrap;
        }
        div {
          color: red;
          font-size: 15rem;

          span {
            text-decoration: line-through;
            color: gray;
            font-size: 10rem;
          }
        }
      }
    }
  }
}
</style>