<template>
  <div>
    <div class="top_ad">
      <van-icon
        name="arrow-left"
        size="24rem"
        color="#8c8989"
        @click="$router.back()"
      />
    </div>

    <van-loading type="spinner" v-if="!detail" />
    <div class="about_con">
      <div class="user_top">
        <div class="user_left">
          <div class="img_p">
            <img :src="detail[0]?.author_img" alt="" />
          </div>
          <div class="n_t">
            <div class="name_u">{{ detail[0]?.author_nickname }}</div>
            <div class="create_time">{{ detail[0]?.create_time }}</div>
          </div>
        </div>
        <div class="user_rigth">+ 关注</div>
      </div>
      <div class="moment_title">
        {{ detail[0]?.moment_title }}
      </div>
      <div class="vid_play"   @click="playVideo">
        <video
          :src="detail[0]?.play_url"
          controls
          class="vvideo"
          id="video"
          ref="vvideo"
         
        ></video>
        <div class="play" v-if="showplay">
          <img src="@/assets/play.png" alt="" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data: function () {
    return {
      detail: [],
      showplay: true,
    };
  },
  created() {
    this.getDetail(this.$route.query.id);
  },
  beforeRouteUpdate(to) {
    this.getDetail(to.query.id);
  },
  methods: {
    playVideo: function () {
    console.log(this.$refs.vvideo);
      if (this.showplay) {
        console.log("bofang");
         this.showplay=false
        this.$refs.vvideo.play()
        console.log(this.$refs.vvideo);
      }else{
          console.log("zanting");
        this.showplay=true
        this.$refs.vvideo.pause()
      }
    },
    getDetail(id) {
      console.log(id);
      this.axios
        .get("https://apis.netstart.cn/xmsc/moment/get_moment_by_idlist?", {
          params: { moment_id_list: id },
        })
        .then((res) => {
          this.detail = res.data.data.moment_list;
          console.log(res);
        });
    },
  },
};
</script>

<style lang="less" scoped>
.vid_play {
  position: relative;
  padding: 0 16rem;
  width: 100%;
  height: 100%;
  z-index: 9;
  .vvideo {
    border-radius: 10rem;
    // position: absolute;
    z-index: 1;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    margin: auto;
  }
  .play {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    margin: auto;
    background-color: #161616f6;
    width: 65rem;
    border-radius: 50%;
    height: 65rem;
    // z-index: 2;
    img {
      width: 50rem;
      height: 50rem;
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 10rem;
      margin: auto;
    }
  }
}
.moment_title {
  text-align: left;
  font-size: 15rem;
  line-height: 28rem;
  color: #000;
  padding: 0 16rem;
  font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
}
.user_top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20rem 16rem;
  position: relative;
  top: 0;
  .user_left {
    display: flex;
    align-items: center;
    justify-content: left;
    .img_p {
      width: 40rem;
      height: 40rem;
      img {
        border-radius: 50%;
        width: 100%;
        height: 100%;
      }
    }
    .n_t {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: left;
      margin-left: 8rem;
      .name_u {
        text-align: left;
        font-weight: 500;
        color: #161616;
        font-size: 14rem;
      }
      .create_time {
        font-weight: 500;
        color: #757575;
        font-size: 12rem;
      }
    }
  }
  .user_rigth {
    display: flex;
    align-items: center;
    justify-content: right;
    padding: 4rem 18rem;
    border: 1rem solid #ff5528;
    border-radius: 16rem;
    color: #fff;
    background: #ff6700;
    border: 0.01rem solid #ff6700;
  }
}
.top_ad {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: sticky;
  padding: 16rem 10rem;
  height: 50rem;
  top: 0;
  z-index: 0;
  background-color: rgb(242, 242, 242);
  .arrow-left {
    width: 100%;
    font-size: 16rem;
    color: #666;
  }
}
</style>