<template>
  <div class="boxcoun">
    <div class="top_ad">
      <van-icon
        name="arrow-left"
        size="24rem"
        color="#8c8989"
        @click="$router.back()"
      />
      <div class="arrow-left">用户结算</div>
    </div>
    <div class="addss" @click="$router.push({ path: '/addaddress' })">
      
      <div v-if="address" class="address_tel"> <div>{{address.name }} {{address.tel.replace(address.tel.substring(3,7), "****")}}</div>
      <div class="city">{{address.province+" "}}{{address.city+" "}}{{address.county+" "}}{{address.addressDetail}}({{address.areaCode}})</div></div>
      <div v-else>添加收货地址</div>
      <van-icon name="arrow" />
      <!-- (address.tel+"").split("").splice(3,4,"****").join("") -->
    </div>
    <div class="ui-line"></div>
    <section class="buy_box">
      <div class="buylist" v-for="(item, index) in buylist" :key="index">
        <div class="buy_fl">
          <img :src="item.goods.img_url ? item.goods.img_url : ''" alt="" />
          <div class="buy_name">{{ item.goods.name }}</div>
        </div>
        <div>
          {{ item.coun }} × <span>{{ item.goods.price }}</span>
        </div>
      </div>
    </section>
    <section class="van_foot">
      <div class="sum_couns">
        <div class="sum_buy">共{{ buylist.length }}件</div>
        <div class="buy_couns">
          合计：<span>{{ price }}</span>
        </div>
      </div>
      <div class="zhifu" @click="goBuy">去支付</div>
    </section>
  </div>
</template>

<script>
import { Toast } from "vant";
export default {
  data: function () {
    return {
      buylist: JSON.parse(localStorage.getItem("buylist")) || null,
      price: 0,
      buyed: JSON.parse(localStorage.getItem("buyed")),
      address: JSON.parse(localStorage.getItem("address")) || null,
    };
  },
  created() {
    this.buylist.forEach((o) => {
      this.price = this.price + o.coun * o.goods.price;
    });
    if (this.price % 1 === 0) {
      this.price = this.price + ".00";
    }
  },
  methods: {
    goBuy: function () {
      if (this.address) {
        this.$router.push({ path: "/order",query:{page:2},});
        localStorage.setItem("buylist", "");
        if (this.buyed) {
          this.buyed = [...this.buylist, ...this.buyed];
          console.log(this.buyed);
          localStorage.setItem("buyed", JSON.stringify(this.buyed));
        } else {
          localStorage.setItem("buyed", JSON.stringify(this.buylist));
        }
        this.buylist = "";
      } else {
        Toast("请添加地址！");
      }
    },
  },
};
</script>

<style lang="less" scoped>
.address_tel{
    text-align: left;
    font-size: 14rem;
    .city{
        color: #999;
        font-size: 10rem;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
        width: 310rem;
    }
}
.van_foot {
  position: fixed;
  bottom: 0;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  // height: 50rem;
  // padding:0 20rem ;
  border-top: 1rem solid lightgrey;
  .sum_couns {
    display: flex;
    justify-content: left;
    align-items: center;
    width: 50%;
    color: #ff4d14;
    font-size: 16rem;
    .buy_couns {
      // width: 20%;
      padding-left: 4rem;
      span {
        font-size: 18rem;
        font-weight: bold;
      }
    }
    .sum_buy {
      padding-left: 10rem;
    }
  }

  .zhifu {
    width: 50%;
    background-color: #ff5722;
    padding: 16rem 0;
    font-size: 16rem;
    color: #fff;
  }
}
.buy_box {
  margin: 0 20rem;
  .buylist {
    display: flex;
    justify-content: space-between;
    align-items: center;

    border-bottom: 1rem solid lightgrey;
    .buy_fl {
      display: flex;
      justify-content: space-between;
      align-items: center;
      .buy_name {
        overflow: hidden;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 2;
        text-align: left;
        width: 200rem;
        text-overflow: ellipsis;
      }
      img {
        width: 70rem;
      }
    }
    font-size: 14rem;
    span {
      color: #000;
      font-weight: bold;
      font-size: 14rem;
    }
  }
}

.ui-line {
  height: 10rem;
  background-color: rgb(243, 243, 243);
}
.boxcoun {
  position: relative;
}
.top_ad {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: sticky;
  padding: 16rem 10rem;
  height: 50rem;
  top: 0;
  z-index: 0;
  background-color: rgb(242, 242, 242);
  .arrow-left {
    width: 100%;
    font-size: 16rem;
    color: #666;
  }
}
.addss {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-content: center;
  // position: relative;
  top: 50rem;
  padding: 20rem;
  color: #000;
  font-size: 16rem;
}
</style>