<template>
  <div id="app">
    <router-view
      :style="{ marginBottom: '50rem' }"
      @change-cart="changecart($event)"
      @change-coun="changecoun($event)"
      @change-list="changelist($event)"
      :obj="obj"
      :sun="sun"
      @delete-list="deleteList($event)"
      :checkedAll="checkedAll"
      @allchange-target="allchangetarget($event)"
      :price="price"
    />
    <nav v-if="$route.meta.showNav">
      <router-link
        :to="link.path"
        v-for="(link, index) in homeicon"
        :key="index"
      >
        <div class="a">
          <img :src="require(`@/assets/${link.ic}.png`)" alt="" />
          <img :src="require(`@/assets/${link.ic}-selected.png`)" alt="" />
          <span >{{ link.title }}</span>
        </div>
      </router-link>
      <div class="sun" v-if="!sun == 0">{{ sun }}</div>
    </nav>
  </div>
</template>
<script>
 
export default {
  data: function () {
    return {
      sun: localStorage.getItem("sun") || 0,
      obj: JSON.parse(localStorage.getItem("addCartlist")) || [],
      homeicon: [
        { ic: "icon-home", title: "首页", path: "/" },
        { ic: "icon-category", title: "分类", path: "/category" },
        { ic: "icon-star", title: "米圈", path: "/discover" },
        { ic: "icon-cart", title: "购物车", path: "/cart" },
        { ic: "icon-user", title: "我的", path: "/user" },
      ],
      g: {},
      check: {},
      checkedAll: JSON.parse(localStorage.getItem("checkedAll")) || false,
      price: JSON.parse(localStorage.getItem("price")) || 0,
    };
  },
  computed: {
    // let obj = JSON.parse(localStorage.getItem("addCartlist"));
  },
  methods: {
    allchangetarget: function (event) {
      // console.log(event, this.obj);
      this.obj.map((o) => {
        o.checked = event;
      });
      let coun = 0;
      this.obj.forEach((o) => {
        if (o.checked == true) {
          coun = coun + o.coun * o.goods.price * 100;
        }
      });
      localStorage.setItem("checkedAll", JSON.stringify(event));
      this.price = coun;
      localStorage.setItem("price", JSON.stringify(coun));
      localStorage.setItem("addCartlist", JSON.stringify(this.obj));
    },
    changelist: function (event) {
      // console.log(event);
      this.obj.map((o) => {
        if (JSON.stringify(o) == JSON.stringify(event.item)) {
          // this.check = { o, index };
          return  o.checked = event.checked;
        }
      });
      localStorage.setItem("addCartlist", JSON.stringify(this.obj));
      this.checkedAll = this.obj.every((o) => o.checked);
      localStorage.setItem("checkedAll", JSON.stringify(this.checkedAll));
      let coun = 0;
      this.obj.forEach((o) => {
        if (o.checked == true) {
          coun = coun + o.coun * o.goods.price * 100;
        }
      });
      this.price = coun;
      localStorage.setItem("price", JSON.stringify(coun));
    },
    deleteList: function (event) {
      this.obj = this.obj.filter(
        (o) => !(JSON.stringify(o) == JSON.stringify(event))
      );
      localStorage.setItem("addCartlist", JSON.stringify(this.obj));
      let sum = 0;
      this.obj.forEach((o) => {
        sum = sum + o.coun;
      });
      this.sun = sum;
      localStorage.setItem("sun", this.sun);
      let coun = 0;
      this.obj.forEach((o) => {
        if (o.checked == true) {
          coun = coun + o.coun * o.goods.price * 100;
        }
      });
      this.price = coun;
      localStorage.setItem("price", JSON.stringify(coun));

      this.checkedAll = this.obj.every((o) => o.checked);
      if (this.obj.length == 0) {
        this.checkedAll = false;
      }
      localStorage.setItem("checkedAll", JSON.stringify(this.checkedAll));
    },
    changecoun: function (event) {
      this.obj.forEach((o, index) => {
        if (JSON.stringify(o.goods) == JSON.stringify(event.item.goods)) {
          this.g = { o, index };
        }
      });
      this.obj?.splice(this.g.index, 1, {
        goods: event.item.goods,
        coun: event.coun,
        checked: event.item.checked,
      });
      localStorage.setItem("addCartlist", JSON.stringify(this.obj));
      let sum = 0;
      this.obj.forEach((o) => {
        sum = sum + o.coun;
      });
      this.sun = sum;
      localStorage.setItem("sun", this.sun);
      let coun = 0;
      this.obj.forEach((o) => {
        if (o.checked == true) {
          coun = coun + o.coun * o.goods.price * 100;
        }
      });
      this.price = coun;
      localStorage.setItem("price", JSON.stringify(coun));
    },
    changecart: function (event) {
      console.log(event);
      let sum = 0;
 this.obj = [
        { goods: event.addbuy, coun: event.coun, checked: false },
        ...this.obj,
      ];
     
      localStorage.setItem("addCartlist", JSON.stringify(this.obj));
      this.obj.forEach((o) => {
        sum = sum + o.coun;
        // console.log(o.coun,sum);
      });
      this.sun = sum;
      localStorage.setItem("sun", this.sun);
      this.checkedAll = this.obj.every((o) => o.checked);
      localStorage.setItem("checkedAll", JSON.stringify(this.checkedAll));
    },
  },
};
</script>

