<template>
  <section>
    <div class="top_cart">
      <van-icon name="arrow-left" size="25rem" color="#777" @click="$router.back()" />
      <div style="font-size: 16rem">购物车</div>
      <img src="@/assets/ipt-icon.png" alt="" @click="$router.push({ path: '/search' })" />
    </div>
    <section class="showCart">
      <ul class="uu">
        <li class="ll" v-for="(item, index) in obj" :key="index">
          <van-checkbox v-model="item.checked" checked-color="#ff6700" class="checkbox"
            @change="changelist(item, item.checked)"></van-checkbox>
          <div class="checkbox2"></div>
          <img :src="item?.goods?.img_url" alt="" />
          <div class="price">
            <div class="name">{{ item.goods?.name }}</div>
            <p>
              售价：{{ item.goods?.price }}元
              <span v-if="item?.goods?.price == !item?.goods?.market_price">￥{{ item?.goods?.market_price }}</span>
            </p>
            <van-stepper class="stepper" v-model="item.coun" @change="onchange(item, item.coun)" />
          </div>
          <van-icon name="delete-o" class="delete" @click="deleteList(item)" />
        </li>
      </ul>
    </section>
    <div class="loadingg">
      <van-loading type="spinner" v-if="!cartlist" size="46" color="red" />
    </div>
    <div class="cartlist" v-if="cartlist">
      <header>
        <img :src="cartlist?.header?.body?.items[0]?.img_url" alt="" />
      </header>
      <ul class="recom_list">
        <li v-for="(li, index) in cartlist?.recom_list" :key="index" @click="
          $router.push({ path: 'showgoods', query: { id: li.action.path } })
        ">
          <van-image :src="li.image_url">
            <template v-slot:loading>
              <van-loading type="spinner" size="20" />
            </template>
          </van-image>
          <!-- <img :src="li.image_url" alt=""> -->
          <div class="link">
            <p>{{ li.name }}</p>
            <div>
              ￥{{ li.price }}
              <span v-if="li.price != li.market_price">￥{{ li.market_price }}</span>
            </div>
          </div>
        </li>
      </ul>
    </div>
    <!-- <div class="footerVan">
  <div class="van_fl">
    <div>共{{}} 金额：</div>
    <div v-text="price"> <div>元</div><div>明显</div></div>
  </div>
<div class="van_btn">
<div class="jxgw" @click="$router.back()">
  继续购物
</div>
<div class="qjs">去结算</div>
</div>
</div> -->
    <div class="footerVan">
      <div class="van_btn">
        <div class="jxgw" @click="$router.back()">
          继续购物
        </div>
        <div class="qjs" @click="onSubmit">去结算</div>

      </div>
    </div>
    <van-submit-bar :price="price" button-text="提交订单" @submit="onSubmit" text-align="left" button-color="transparent"
      label="金额：">
      <van-checkbox v-model="checked" checked-color="#ff6700" class="allinput" @click="allCheck(checked)">
      </van-checkbox>
      <!-- <span class="allspan">全选</span> -->
    </van-submit-bar>
  </section>
</template>

<script>
import { Toast } from "vant";
export default {
  props: {
    obj: Array,
    checkedAll: Boolean,
    price: Number,
  },
  data: function () {
    return {
      cartlist: null,
      checked: JSON.parse(localStorage.getItem("checkedAll")) || false,
    };
  },

  created() {
    this.axios.get("https://apis.netstart.cn/xmsc/cart_recom").then((res) => {
      this.cartlist = res.data.data;
      // console.log(res.data.data);
    });
  },
  computed: {

  },
  watch: {
    checkedAll: function (n) {
      this.checked = n;
      localStorage.setItem("checkedAll", JSON.stringify(n));
    },
  },
  methods: {
    changelist: function (item, checked) {
      //  this. price
      // console.log(item, checked);
      this.$emit("change-list", { item, checked });
    },
    allCheck: function (event) {
      // console.log(event);
      this.$emit("allchange-target", event);
      // localStorage.setItem("checked",JSON.stringify(event))
    },
    onSubmit: function () {

      console.log(this.obj.some(o => o.checked == true));
      if (this.obj.some(o => o.checked == true)) {
        localStorage.setItem("buylist", JSON.stringify(this.obj.filter(o => o.checked == true)))
        this.$router.push({ path: '/checkout' })
      } else {
        Toast("请勾选择商品");
      }

    },

    deleteList: function (item) {
      this.$emit("delete-list", item);
    },
    onchange: function (item, coun) {
      this.$emit("change-coun", { item, coun });
      // console.log(item,coun);
    },
  },
};
</script>

<style lang="less" scoped>
.footerVan {
  width: 70%;
  //  color:transparent ;
  position: fixed;
  bottom: 0;
  right: 0;
  height: 50rem;
  // background-color: #fff;
  z-index: 999999;

  .van_btn {
    display: flex;
    justify-content: right;
    align-items: center;

    .jxgw {
      font-size: 16rem;
      line-height: 50rem;
      padding: 0 20rem;
      background-color: #f4f4f4;
    }

    .qjs {
      font-size: 16rem;
      line-height: 50rem;
      background: #ff6700;
      padding: 0 30rem;
      color: #fff;

    }
  }
}

.allinput {
  width: 20rem;
  height: 20rem;
  //  padding-right: 2rem;
  margin-right: 4rem;
}

.allspan {
  padding: 0 10rem;
  line-height: 20rem;
  font-size: 16rem;
  color: #000;
}

.top_cart {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10rem;

  img {
    width: 30rem;

  }
}

.showCart {
  background-color: rgb(234, 231, 235);
  width: 100%;
  padding: 10rem 0;

  .loadingg {
    position: absolute;
    margin-top: 200rem;
    left: 45%;
  }

  .uu {
    width: 100%;
    margin: auto;

    .ll {
      width: 100%;
      display: flex;
      align-content: center;
      justify-content: left;
      background: #fff;
      margin: 6rem 0;
      padding: 10rem 0;
      border-radius: 4rem;
      position: relative;

      .checkbox {
        // position: absolute;
        // top: 0;
        width: 40rem;

        // margin: 0;
        // display: block;
        margin-left: 10rem;

      }

      //  .checkbox2{
      //      position: absolute;
      //     width: 20rem;
      //     height: 20rem;
      //     border-radius: 50%;
      //     background-color: rgba(0, 42, 255, 0.378);
      //     margin-left: 10rem;
      //   }

      img {
        width: 80rem;
        margin-left: 10rem;
        border: 1rem solid rgb(226, 222, 222);
        border-radius: 2rem;
      }

      .delete {
        width: 20rem;
        // line-height: 80rem;
        position: absolute;
        bottom: 16rem;
        right: 10rem;
        margin-right: 6rem;
        font-size: 20rem;
      }

      .price {
        width: 100%;
        display: flex;
        flex-direction: column;
        padding-left: 10rem;
        justify-content: center;
        text-align: left;

        p {
          color: #ff5934;
          font-size: 14rem;
          padding-bottom: 4rem;
        }

        .name {
          overflow: hidden;
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 2;
          text-align: left;
          text-overflow: ellipsis;
          padding-bottom: 4rem;
          font-size: 14rem;
          color: #5c5b5b;
          width: 230rem;
        }
      }
    }
  }
}

.cartlist {
  width: 100%;

  header {
    img {
      width: 100%;
    }
  }

  .recom_list {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    li {
      width: 49.5%;

      img {
        width: 100%;
      }

      .link {
        margin: 10rem 20rem;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: left;
        text-align: left;

        p {
          font-size: 15rem;
          width: 160rem;
          text-overflow: ellipsis;
          overflow: hidden;
          white-space: nowrap;
        }

        div {
          color: red;
          font-size: 15rem;

          span {
            text-decoration: line-through;
            color: gray;
            font-size: 10rem;
          }
        }
      }
    }
  }
}
</style>