<template>
  <section>
    <header class="search-top">
      <van-icon
        name="arrow-left"
        size="25rem"
        color="#777"
        @click="$router.back()"
      />
      <input type="text" placeholder="搜索商品名称" v-model="text" @keyup.enter="changequery(text)"/>
      <div class="cross" @click="deleteText" v-show="scshow">
        <van-icon name="cross" />
      </div>
      <img src="@/assets/ipt-icon.png" alt="" @click="changequery(text)"/>
    </header>
    <dir class="loading">
      <van-loading type="spinner" v-if="!discover_list" size="46" color="red" />
    </dir>
    <div class="discover" v-if="historyData||showHis">
     <div class="h-his" >
       <p>搜索历史</p>
       <van-icon name="delete-o" class="delete-o" @click="deleteHis"/>
     </div>
      <div class="historyData">
        <div  v-for="(his,index) in historyData" :key="index" @click="changequery(his)" class="his">
          {{his}}
        </div>
      </div>
    </div>
    <div class="discover" v-if="discover_list">
      <p>搜索发现</p>
      <ul>
        <li v-for="list in discover_list" :key="list.id" @click="changequery(list.desc)">{{ list.desc }}</li>
      </ul>
    </div>
  <div>
    <!-- <ProductData /> -->
  </div>
    <div
      class="mask"
      @click="showmask = !showmask"
      v-show="suggestion && showmask"
    >
      <ul>
        <li v-for="(item, index) in suggestion" :key="index" @click="changequery(item.title)">
          {{ item.title }}
        </li>
      </ul>
    </div>
  </section>
</template>

<script>
// import ProductData from '../components/ProductData.vue'
var _ = require("lodash");
export default {
components:{
  // ProductData
},
  data: function () {
    return {
      text: null,
      suggestion: null,
      showmask: true,
      scshow: false,
      discover_list: null,
      // showproduct:true,
      showHis:false,
      // historyData:[]
      historyData:JSON.parse(localStorage.getItem("historyData"))||[]
    };
  },
  created() {
    this.axios
      .get("https://apis.netstart.cn/xmsc/hisearch/se_default")
      .then((res) => {
        this.discover_list = res.data.data.discover_list;
        // console.log(res.data.data.discover_list);
      });
      if (this.historyData==!'') {
        this.showHis=true
      }
  },
  watch: {
    text: _.debounce(function (newtext) {
      if (newtext) {
        this.scshow = true;
        this.axios
          .get("https://apis.netstart.cn/xmsc/hisearch/suggestion_v3", {
            params: { query: newtext },
          })
          .then((res) => {
            // console.log(res.data.data.list);
            this.suggestion = res.data.data?.list;
            this.showmask = true;
          });
      } else {
        this.scshow = false;
        this.showmask = false;

      }
    }, 1000),
  },
  methods: {
    deleteHis:function () {
      this.historyData=''
        localStorage.setItem("historyData", JSON.stringify(this.historyData));
      this.showHis=false
    },
    deleteText: function () {
      this.text = "";
      this.scshow = false;
      this.showmask = false;
    },
    changequery:function (data) {
        this.showHis=true
      this.historyData=[...new Set([data, ...this.historyData])]
      // console.log(this.historyData);
      // console.log(data);
    localStorage.setItem("historyData", JSON.stringify(this.historyData));
      //  localStorage.setItem("historyData", JSON.stringify(this.historyData));
      this.showproduct=false
    if (data) {
        this.$router.push({
              path: "/search/list",
              query: { key: data },
            });
    }
    }
  },
};
</script>
 
<style lang="less" scoped>
.historyData{
display: flex;
flex-wrap: wrap;
.his{
  background-color: #e6e4e496;
  padding: 2rem 6rem;
  margin-right: 4rem;
  border-radius: 8rem;
  margin-bottom: 4rem;
}
}
.search-top {
  height: 40rem;
  background-color: rgb(244, 241, 241);
  display: flex;
  justify-content: space-around;
  align-items: center;
  position: relative;
  img {
    width: 30rem;
  }
  .cross {
    position: absolute;
    left: 310rem;
    background-color: rgb(241, 236, 236);
    border-radius: 50%;
  }
  input {
    width: 280rem;
    padding: 2rem 10rem;
    border: none;
    font-size: 16rem;
  }
}
.mask {
  position: absolute;
  top: 40rem;
  background-color: rgba(128, 128, 128, 0.258);
  width: 100%;
  height: 570rem;
  ul {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    background-color: #fff;
    li {
      font-size: 14rem;
      border-bottom: 1rem solid gray;
      width: 330rem;
      text-align: left;
      line-height: 40rem;
      padding: 0 16rem;
    }
  }
}
.discover {
  margin: 30rem 20rem;
    .h-his{
 display: flex;
    justify-content: space-between;
    align-items: center;
     .delete-o{
      font-size: 16rem;
    }
    }
  P {
    font-size: 14rem;
    color: #000;
    font-weight: bold;
    text-align: left;
    padding-bottom: 10rem;
  }
  ul {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    li {
      width: 50%;
      text-align: left;
      // margin: 10rem;
      padding: 10rem;
      font-size: 12rem;
    }
  }
}
</style>