<template>
  <section>
    <dir class="loading">
      <van-loading type="spinner" v-if="!productData" size="46" color="red" />
    </dir>
    <div v-if="productData">
      <header class="search-top">
        <van-icon
          name="arrow-left"
          size="25rem"
          color="#777"
          @click="$router.back()"
        />
        <input
          type="text"
          placeholder="搜索商品名称"
          v-model="text"
          @keyup.enter="
            getDetail(text);
            showmask = false;
          "
        />
        <div class="cross" @click="deleteText" v-show="scshow">
          <van-icon name="cross" />
        </div>

        <img src="@/assets/ipt-icon.png" alt="" />
      </header>
      <section class="classes">
        <van-tabs background="#f2efef" color="transparent">
          <van-tab v-for="(item, index) in productData?.classes" :key="index">
            <template #title>
              <div class="call" @click="getDetail(item.class_name)">
                <van-image :src="item.class_icon" class="class_icon">
                  <template v-slot:loading>
                    <van-loading type="spinner" size="20" />
                  </template>
                </van-image>
                <!-- <img :src="item.class_icon" alt="" class="class_icon" /> -->
                <div class="class_name">{{ item.class_name }}</div>
              </div>
            </template>
          </van-tab>
        </van-tabs>
      </section>
      <section class="c-card">
        <ul class="ull">
          <li
            class="lii"
            v-for="(list_v2, index) in productData?.list_v2"
            :key="index"
            @click="
              $router.push({
                path: '/showgoods',
                query: { id: list_v2.body.product_id },
              })
            "
          >
            <van-image :src="list_v2.body.image">
              <template v-slot:loading>
                <van-loading type="spinner" size="20" />
              </template>
            </van-image>
            <div class="card_fr">
              <div class="name">{{ list_v2.body.name }}</div>
              <div
                class="desc"
                v-text="list_v2.body.desc.replace(/\<.*?\>/g, '')"
              ></div>
              <div class="tops">
                <div
                  class="top"
                  v-for="(li, indes) in list_v2.body?.class_parameters?.list"
                  :key="indes"
                >
                  <div class="top_li">{{ li.top_title }}</div>
                  <!-- <div class="value">{{ li.value }}</div> -->
                  <div
                    class="value"
                    v-text="li.value.slice(0, li.value.indexOf('+'))"
                  ></div>
                </div>
              </div>
              <div class="prices">
                <div class="price">￥{{ list_v2.body.price }}</div>
                <span v-if="!(list_v2.body.price == list_v2.body.market_price)"
                  >￥{{ list_v2.body.market_price }}</span
                >
                <div
                  v-for="(label, la) in list_v2.body?.sale_label?.activity_label"
                  :key="la"
                  class="piao"
                >
                  <!-- <img :src="label.img_url" alt=""> -->

                  <van-image :src="label.img_url">
                    <template v-slot:loading>
                      <van-loading type="spinner" size="20" />
                    </template>
                  </van-image>
                </div>
              </div>

              <div class="totol">
                {{ list_v2?.body.comments_total }}条评价
                <span>{{ list_v2.body.satisfy_per }}满意</span>
              </div>
            </div>
          </li>
        </ul>
      </section>
      <div
        class="mask"
        @click="showmask = !showmask"
        v-if="suggestion && showmask"
      >
        <ul class="u_ul">
          <li
            class="l_li"
            v-for="(item, index) in suggestion"
            :key="index"
            @click="getDetail(item.title)"
          >
            {{ item.title }}
          </li>
        </ul>
      </div>
    </div>
  </section>
</template>

<script>
var _ = require("lodash");
export default {
  data: function () {
    return {
      productData: null,
      text: null,
      suggestion: null,
      showmask: false,
      scshow: false,
      historyData: JSON.parse(localStorage.getItem("historyData")) || [],
    };
  },
  created() {
    this.getDetail(this.$route.query.key);
  },
  beforeRouteUpdate(to) {
    this.getDetail(to.query.key);
  },
  methods: {
    // changequery:function (histext) {

    // },
    //  localStorage.setItem("historyData", JSON.stringify(this.historyData));
    getDetail: function (key) {
      this.axios
        .get("https://apis.netstart.cn/xmsc/hisearch/query_v3?", {
          params: { query: key },
        })
        .then((res) => {
          // console.log(123, res.data.data);
          this.productData = res.data.data;
        });
      this.historyData = [...new Set([key, ...this.historyData])];
      localStorage.setItem("historyData", JSON.stringify(this.historyData));
    },
    deleteText: function () {
      this.text = "";
      this.scshow = false;
      this.showmask = false;
    },
  },
  watch: {
    text: _.debounce(function (newtext) {
      if (newtext) {
        this.scshow = true;
        this.axios
          .get("https://apis.netstart.cn/xmsc/hisearch/suggestion_v3", {
            params: { query: newtext },
          })
          .then((res) => {
            // console.log(res.data.data.list);
            if (res) {
              this.suggestion = res.data.data?.list;
              this.showmask = true;
            } else {
              this.suggestion = null;
              this.showmask = false;
            }
          });
      } else {
        this.scshow = false;
        this.showmask = false;
      }
    }, 1000),
  },
};
</script>

<style lang="less" >
.c-card {
  .ull {
    .lii {
      display: flex;
      align-items: center;
      width: 100%;
      border-bottom: 1rem solid rgb(131, 130, 130);
      padding: 10rem 0;
      img {
        width: 120rem;
      }
      .card_fr {
        width: 250rem;
        display: flex;
        flex-direction: column;
        .name {
          font-size: 12rem;
          text-align: left;
        }
        .desc {
          width: 98%;
          overflow: hidden;
          display: -webkit-box;
          -webkit-box-orient: vertical;
          -webkit-line-clamp: 2;
          color: #f74f4f;
          margin-top: 6rem;
          font-size: 12rem;
          text-align: left;
        }
        .tops {
          display: flex;
          align-items: center;
          justify-content: space-between;
          margin-top: 6rem;
          .top {
            width: 100%;
            font-size: 10rem;
            // padding: 10rem;
            padding: 4rem;
            &:nth-child(2) {
              border-left: 1rem solid #dad5d5;
              border-right: 1rem solid #dad5d5;
            }
            .top_li {
              color: rgb(32, 31, 31);
              font-size: 10rem;
              // width: 100%;
              white-space: nowrap;
            }
            .value {
              color: #4f4e4e;
              font-size: 10rem;
              white-space: nowrap;
              overflow: hidden;
              text-overflow: ellipsis;
              width: 65rem;
              
            }
          }
        }
        .prices {
          display: flex;
          justify-content: left;
          align-items: center;
          .price {
            color: #f53105;
            font-size: 16rem;
            line-height: 20rem;
          }
          span {
            margin-left: 4rem;
            font-size: 8rem;
            line-height: 20rem;
            color: rgb(103, 103, 103);
            text-decoration: line-through;
          }
          img {
            width: 46rem;
            padding: 0 6rem;
          }
        }
        .totol {
          text-align: left;
          font-size: 8rem;

          span {
            display: inline-block;
            padding: 0 8rem;
          }
        }
      }
    }
  }
}
.classes {
  .van-tabs {
    width: 100%;
    .van-tabs__wrap {
      height: 120rem;
      // margin: 10rem 0;
    }
    .call {
      background: white;
      padding: 4rem;
      box-sizing: border-box;
      width: 100%;
      border-radius: 2rem;
      .class_icon {
        width: 60rem;
        height: 100%;
      }
      .class_name {
        font-size: 8rem;
        width: 100%;
        overflow: hidden;
        white-space: nowrap;
      }
    }
  }
}

.search-top {
  height: 50rem;
  background: white;
  display: flex;
  justify-content: space-around;
  align-items: center;
  position: relative;
  img {
    width: 30rem;
  }
  .cross {
    position: absolute;
    left: 310rem;
    background-color: rgb(239, 234, 234);
    border-radius: 50%;
  }
  input {
    width: 280rem;
    // padding: 4rem 4rem;
    border: none;
    font-size: 16rem;
    line-height: 28rem;
    border-radius: 2rem;
    border: 1rem solid #dfdede;
    border-radius: 4rem;
    background-color: #f9f9f9ba;
  }
}
.mask {
  position: absolute;
  top: 40rem;
  background-color: rgba(128, 128, 128, 0.258);
  width: 100%;
  height: 570rem;
  .u_ul {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    background-color: #fff;
    .l_li {
      font-size: 14rem;
      border-bottom: 1rem solid gray;
      width: 330rem;
      text-align: left;
      line-height: 40rem;
      padding: 0 16rem;
    }
  }
}
</style>