<template>
  <div>
    <div class="top_ad">
      <van-icon
        name="arrow-left"
        size="24rem"
        color="#8c8989"
        @click="$router.back()"
      />
      <div class="arrow-left">文章详情</div>
      <van-icon name="ellipsis" size="18rem" @click="show2 = !show2"/>
    </div>
    <van-loading type="spinner" v-if="!detail" />
    <div class="top_title">{{ detail?.data?.title }}</div>
    <div>
      <div class="user_top">
        <div class="user_left">
          <div class="img_p">
            <img :src="detail?.data?.icon" alt="" />
          </div>
          <div class="n_t">
            <div class="name_u">{{ detail?.data?.nickname }}</div>
            <div class="create_time">{{ detail?.data?.create_time }}</div>
          </div>
        </div>
        <div
          class="user_rigth"
         v-if="guangZhu.includes(detail?.data?.id) ? false : true"
          @click="addSju(detail?.data?.id)"
        >
          关注
        </div>
        <div
          class="yiguangz"
          v-if="guangZhu.includes(detail?.data?.id) ? true : false"
           @click="addSju(detail?.data?.id)"
        >
          已关注
        </div>
      </div>
      <div v-html="detail?.data?.article_h5" class="article_h5"></div>
    </div>
     <van-popup
                      class="popup_tan"
                      v-model="show2"
                      round
                      position="bottom"
                      :style="{ height: '25%' }"
                    >
                      <div class="jubao">举报<van-icon name="arrow" /></div>
                      <div class="jinggao">辱骂攻击、广告、违法违规</div>
                      <div class="ctrl-btn" @click="show2 = !show2">取消</div>
                    </van-popup>
    <div class="footer">
      <div
        class="buy_p"
        @click="
          $router.push({
            path: '/showgoods',
            query: { id: detail?.data?.products[0]?.product_id },
          })
        "
        v-text="detail?.data?.products[0]?.price?'￥' +detail?.data?.products[0]?.price +'起' +'立即购买':'立即购买'"
      >
       
        <van-icon
          name="arrow"
        />
      </div>
      <img src="@/assets/share.png" alt="" />
      <div>
        <img src="@/assets/msgf.png" alt="" /><span
          v-text="detail?.data?.comment_num ? detail?.data?.comment_num : ''"
        ></span>
      </div>
      <div>
        <van-icon
          name="good-job-o"
          size="18rem"
          :color="arrdianzs.includes(detail?.data?.id) ? 'red' : ''"
          @click="dianzans(detail?.data?.id)"
        />
        <span
          v-text="
            arrdianzs.includes(detail?.data?.praise_num)
              ? detail?.data?.praise_num * 1 + 1
              : detail?.data?.praise_num
          "
        >
        </span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data: function () {
    return {
      detail: [],
      arrdianzs: JSON.parse(localStorage.getItem("arrdianzs")) || [],
      guangZhu: JSON.parse(localStorage.getItem("guangZhu")) || [],
      show2:false
    };
  },
  created() {
    this.getDetail(this.$route.query.id);
  },
  beforeRouteUpdate(to) {
    this.getDetail(to.query.id);
  },
  methods: {
    getDetail(id) {
      this.axios
        .get("https://apis.netstart.cn/xmsc/communicate/article_detail?", {
          params: { id: id },
        })
        .then((res) => {
          this.detail = res.data;
          // console.log(res.data);
        });
    },
    addSju:function (event) {
      // console.log(event);
      if (this.guangZhu.includes(event)) {
        this.guangZhu = this.guangZhu.filter((o) => {
          return !(o == event);
        });
      } else {
        this.guangZhu = [...this.guangZhu, event];
      }
      localStorage.setItem("guangZhu", JSON.stringify(this.guangZhu));
    },

    dianzans: function (event) {
      console.log(event);
      if (this.arrdianzs.includes(event)) {
        this.arrdianzs = this.arrdianzs.filter((o) => {
          return !(o == event);
        });
      } else {
        this.arrdianzs = [...this.arrdianzs, event];
      }
      localStorage.setItem("arrdianzs", JSON.stringify(this.arrdianzs));
    },
  },
};
</script>

<style lang="less" scoped>
// .van-list{
//   .van-cell{
//     padding: 0;
//   }
// }
.popup_tan {
  padding: 10rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  .jubao {
    color: #333;
    font-size: 14rem;
    margin-bottom: 8rem;
  }
  .jinggao {
    font-size: 10rem;
    color: #ccc;
  }
  .ctrl-btn {
    margin: 25rem 13rem;
    background-color: rgba(0, 0, 0, 0.04);
    border-radius: 33em;
    line-height: 46rem;
    font-size: 14rem;
    width: 280rem;
    text-align: center;
  }
}
.yiguangz {
  padding: 4rem 10rem;
  border-radius: 20rem;
  border: 1rem solid #757575;
  color: #757575;
}
.footer {
  display: flex;
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 50rem;
  background-color: rgb(245, 239, 239);
  align-items: center;
  justify-content: space-around;
  .buy_p {
    padding: 8rem 10rem;
    justify-content: center;
    background: #ff5528;
    color: #fff;
    text-align: center;
    border-radius: 20rem;
    font-size: 13rem;
  }
  img {
    width: 20rem;
  }
  div {
    display: flex;
    align-items: center;
    color: #757575;
    span {
      font-size: 13rem;
      padding-left: 3rem;
    }
  }
}
.top_title {
  text-align: left;
  color: #333;
  font-size: 24rem;
  font-weight: 600;
  padding: 0 16rem;
}
.article_h5 {
  padding: 0 16rem;
  text-align: left;
  font-family: FZLTCHPro--GB1;
  font-weight: 400;
  color: rgb(21, 21, 21);
  font-size: 14rem;
  line-height: 26rem;
  word-spacing: 20rem;
  letter-spacing: 2rem;
  overflow: hidden;
  img {
    border-radius: 10rem;
  }
}
.user_top {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20rem 16rem;
  .user_left {
    display: flex;
    align-items: center;
    justify-content: left;
    .img_p {
      width: 30rem;
      height: 30rem;
      img {
        border-radius: 50%;
        width: 100%;
        height: 100%;
      }
    }
    .n_t {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: left;
      margin-left: 8rem;
      .name_u {
        text-align: left;
        font-weight: 500;
        color: #161616;
      }
      .create_time {
        font-weight: 500;
        color: #757575;
        font-size: 12rem;
      }
    }
  }
  .user_rigth {
    display: flex;
    align-items: center;
    justify-content: right;
    padding: 4rem 18rem;
    border: 1rem solid #ff5528;
    border-radius: 16rem;
    color: #ff5528;
  }
}
.top_ad {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: sticky;
  padding: 16rem 10rem;
  height: 50rem;
  top: 0;
  z-index: 0;
  background-color: #fff;
  .arrow-left {
    width: 100%;
    font-size: 16rem;
    color: #666;
  }
}
</style>