<template>
  <section>
    <header class="top-pp">
      <van-icon
        name="arrow-left"
        size="20rem"
        color="#bab7b9"
        @click="$router.back()"
      />
      <div class="page_title">{{ detail?.page_title }}</div>
      <img src="@/assets/ipt-icon.png" alt="" @click="$router.push({path:'/search'})"/>
    </header>
    <div class="title_top">
      <van-tabs v-model="active" class="van-tabs">
        <van-tab
          v-for="(list, index) in detail?.top_tab"
          :key="index"
          class="van-tab"
        >
          <template #title>
            <div class="title">
              <van-image :src="list.img_url" class="img">
                <template v-slot:loading>
                  <van-loading type="spinner" size="20" />
                </template>
              </van-image>
              <!-- <img :src="list.img_url" alt="" class="img" /> -->
              <p>{{ list.name }}</p>
            </div>
          </template>
          <section class="alllist">
            <div
              class="item"
              v-for="(item, index) in detail.product"
              :key="index"
               @click="gggggggg(item.product_id)"
            >
              <van-image :src="item.puzzle_url">
                <template v-slot:loading>
                  <van-loading type="spinner" size="20" />
                </template>
              </van-image>
              <div class="cont_fr">
                <div class="shang">
                  <div class="name">{{ item.name }}</div>
                <div class="product_desc">{{ item.product_desc }}</div>
                </div>
                <div class="xia">
                  <div class="price">￥{{item.price }}</div>
                <div class="comments_total">
                  {{ item.comments_total }}条评价
                  <span>{{ item.satisfy_per }}</span>
                </div>
                </div>
              </div>
            </div>
          </section>
        </van-tab>
      </van-tabs>
    </div>
  </section>
</template>

<script>
export default {
  data: function () {
    return {
      detail: null,
      yyy: 0,
      active: 0,
      activeyyy: 0,
    };
  },
  created() {
    this.getDetail(this.$route.query.id);
  },
  beforeRouteUpdate(to) {
    this.getDetail(to.query.id);
  },
  methods: {
    gggggggg:function name(n) {
      console.log(n);
          this.$router.push({ path: "showgoods", query: { id:n} });
    },
    getDetail(id) {
      // console.log(id);
      this.axios
        .get("https://apis.netstart.cn/xmsc/product/all_product?", {
          params: { cat_id: id },
        })
        .then((res) => {
          this.detail = res.data.data;
          // console.log(res.data.data);
          // var arr=[]
          // arr.forEach
          this.detail.top_tab.forEach((o, index) => {
            if (o.category_id == id) {
              //  console.log(index);
              this.active = index;
            }
          });
          //   console.log(this.active);
        });
    },
  },
};
</script>

<style lang="less" >
.alllist {
 margin: 10rem;
  .item{
     display: flex;
  justify-content: left;
  align-items: center;
  width: 100%;
  border-bottom: 1rem solid gainsboro;
  padding: 10rem 0;
img {
    width: 100rem;
    height: 100rem;
    background-color: rgba(247, 243, 248, 0.662);
    border-radius: 4rem;
  }
  .cont_fr {
    display: flex;
    flex-direction: column;
    text-align: left;
    justify-content: space-between;
    padding: 0 10rem;
    .shang{
  .name{
      text-overflow: ellipsis;
      white-space: nowrap;
      overflow: hidden;
      width: 200rem;
      color: #000;
    }
    .product_desc{
      font-size: 8rem;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 2;
      overflow: hidden;
       width: 200rem;
    }
    }
  .xia{
    padding-top: 10rem;
    text-align: left;
    .price{
      color: #f75f5f;
      font-size: 14rem;
    }
    .comments_total{
      font-size: 10rem;
    }
  }
  }
  }
  
}

.title_top {
  width: 375rem;
  .van-tabs {
    width: 100%;
    // height: 150rem;
    margin: 10rem 0;
    .van-tabs__wrap {
      height: 80rem;
      width: 100%;
    }
    .van-tab {
        // width: 100%;
        
      .title {
       height: 100rem;
      //  width: 150rem;
        .img {
          width: 50rem;
        }
        p{
          // height: 20rem;
          font-size: 8rem;
        }
      }
    }
  }
}

.top-pp {
  width: 100%;
  height: 40rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10rem;
  background-color: rgba(233, 236, 238, 0.747);
  img {
    width: 30rem;
  }
  .page_title {
    font-size: 14rem;
    color: rgb(120, 116, 116);
  }
}
</style>