<template>
  <section class="miq_mi">
    <header class="discover_top">
      <div class="small_bell">
        <img class="small_bell_img" src="@/assets/small_bell.png" alt="" />
      </div>
      <div class="me_user" @click="$router.push({path:'/user',})">
        <img class="me_user_img" src="@/assets/user.png" alt="" />
      </div>
      <van-tabs
        line-width="8rem"
        v-model="activeName"
        color="#333"
        title-active-color="#333"
        :ellipsis="false"
        title-inactive-color="#999"
      >
        <van-tab title="关注" name="a"></van-tab>
        <van-tab title="推荐" name="b">
          <section class="cont">
            <div class="ulul">
              <van-list
                v-model="loading"
                :finished="finished"
                finished-text="没有更多了"
                @load="onLoad"
              >
                <van-cell v-for="(rec_item, index) in tuijiandata" :key="index">
                  <div class="lili" v-if="rec_item">
                    <div v-if="rec_item?.question" class="question">
                      <p>
                        <span>问</span
                        >{{ rec_item.content?.question?.question }}
                      </p>
                      <div
                        v-for="answer in rec_item.content?.question
                          ?.answer_list_v2"
                        :key="answer.answer"
                        class="answer"
                      >
                        <img :src="answer.icon" alt="" />

                        <div class="text">{{ answer.answer }}</div>
                      </div>
                    </div>
                    <div v-if="rec_item.content?.nickname">
                      <div class="name_pic">
                        <div class="name_pic_left">
                          <img
                            :src="
                              rec_item.content?.icon
                                ? rec_item.content?.icon
                                : ''
                            "
                            alt=""
                          />
                          <span>{{ rec_item.content?.nickname }}</span>
                        </div>
                        <div class="name_pic_r" @click="show2 = !show2">
                          <img src="@/assets/san.png" alt="" />
                        </div>
                        <van-popup
                          class="popup_tan"
                          v-model="show2"
                          round
                          position="bottom"
                          :style="{ height: '25%' }"
                        >
                          <div class="jubao">举报<van-icon name="arrow" /></div>
                          <div class="jinggao">辱骂攻击、广告、违法违规</div>
                          <div class="ctrl-btn" @click="show2 = !show2">
                            取消
                          </div>
                        </van-popup>
                      </div>
                      <div class="c_title"  @click="goPage(rec_item.content)">{{ rec_item.content?.title }}</div>
                      <div class="content"  @click="goPage(rec_item.content)">{{ rec_item.content?.content }}</div>
                      <div
                        class="show_pics1"
                        v-if="rec_item.content?.pic_list.length == 1"
                      >
                        <div
                          class="show_pic"
                          v-for="(pic, index) in rec_item.content?.pic_list"
                          :key="index"
                          @click="viewimg(index,rec_item.content?.pic_list);"
                        >
                          <van-image :src="pic.img" fit="cover">
                            <template v-slot:loading>
                              <van-loading type="spinner" size="20" />
                            </template>
                          </van-image>
                        </div>
                      </div>
                      <div
                        class="show_pics2"
                        v-else-if="rec_item.content?.pic_list.length"
                      >
                        <div
                          class="show_pic"
                          v-for="(pic, index) in rec_item.content?.pic_list"
                          :key="index"
                           @click="viewimg(index,rec_item.content?.pic_list)"
                        >
                          <van-image :src="pic.img" fit="cover">
                            <template v-slot:loading>
                              <van-loading type="spinner" size="20" />
                            </template>
                          </van-image>
                        </div>
                      </div>
                      <div class="tag_list" v-if="rec_item.content?.tag_list">
                        <div
                          v-for="(tag_list, index) in rec_item.content
                            ?.tag_list"
                          :key="index"
                          class="tag_item"
                           @click="taglist(tag_list.id)"
                        >
                          <template v-if="tag_list.tag_id == ''">
                            <img :src="tag_list.img" alt="" />
                            <div class="tag" >{{ tag_list.tag }}</div>
                          </template>
                          <template v-else-if="tag_list.tag_id">
                            <img src="@/assets/huati.png" alt="" />
                            <div class="tag">{{ tag_list.tag }}</div>
                          </template>
                        </div>
                      </div>
                      <div class="hot_com" v-if="rec_item.content?.hot_comment">
                        <div class="comment-m">
                          <span class="name-m"
                            >{{ rec_item.content?.hot_comment?.name }}:
                          </span>
                          {{ rec_item.content?.hot_comment?.comment }}
                        </div>
                      </div>
                      <div class="footer">
                        <img src="@/assets/share.png" alt="" />
                        <div>
                          <img src="@/assets/msgf.png" alt="" /><span
                            @click="showComment(rec_item.content.content_id,rec_item.content?.comment_num)"
                            >{{ rec_item.content?.comment_num }}</span
                          >
                        </div>
                        <div>
                          <van-icon name="good-job-o" size="18rem" :color="arrdianz.includes(rec_item?.content?.id)?'red':''" 
                          @click="dianzan(rec_item?.content?.id);"/> 
                          <span v-text="arrdianz.includes(rec_item?.content?.id)&&!rec_item.content?.praise_num.includes('w')? rec_item.content?.praise_num*1+1: rec_item.content?.praise_num">
                       </span>
                        </div>
                      </div>
                        <div class="ui-line"></div>
                    </div>
                  </div>
                </van-cell>
              </van-list>
            </div>
          </section>
        </van-tab>
        <van-tab
          title="雷军年度演讲"
          name="c"
          :url="discoverdata[0]?.h5_url ? discoverdata[0]?.h5_url : ''"
        ></van-tab>
        <van-tab title="小米发布" name="d"></van-tab>
      </van-tabs>
    </header>

    <van-popup
      v-model="show"
      round
      position="bottom"
      closeable
      :style="{ height: '60%' }"
    >
      <div class="avatar_s" v-if="!(answer_list.length==0)">
        <p class="p_text">共{{ cun }}条评论</p>
        <div v-for="(an, index) in answer_list" :key="index" class="avatar" >
          <div class="img_pic">
            <van-image :src="an.avatar ? an.avatar : ''" round fit="cover" >
              <template v-slot:loading>
                <van-loading type="spinner" size="20" />
              </template>
            </van-image>
          </div>
          <div class="fl">
            <div class="nickname_top">
              <div class="nickname">{{ an.nickname }}</div>
              <div class="create_time">{{ an.create_time }}</div>
            </div>
            <div class="answer_content">{{ an.answer_content }}</div>
          </div>
          <div class="good-job">
             <van-icon name="good-job-o" size="18rem" :color="goodjob.includes(an.user_id)?'red':''" 
                          @click="min_dianzan(an.user_id);"/> <div class="goodjob" v-text="goodjob.includes(an.user_id)?an.dislike_num*1+1:an.dislike_num"></div>
          </div>
        </div>
      </div>
      <div class="van_bott">
          <img class="pen" src="@/assets/pen.png" alt=""><input class="addtext" placeholder="来说点什么" type="text" name="" id="">
        </div>
    </van-popup>
  </section>
</template>

<script>
import Vue from 'vue';
import { ImagePreview } from 'vant';
Vue.use(ImagePreview);
import {mapMutations} from "vuex"
export default {
  data: function () {
    return {
      discoverdata: [],
      tuijiandata: [],
      answer_list: [],
      show: false,
      show2: false,
      activeName: "b",
      loading: false,
      finished: false,
      after: 0,
      cun:0,
      arrdianz:JSON.parse(localStorage.getItem("arrdianz"))|| [],
      goodjob:JSON.parse(localStorage.getItem("goodjob"))|| []
    };
  },
  created() {
 
    this.axios
      .get("https://apis.netstart.cn/xmsc/communicate/home_conf")
      .then((res) => {
        this.discoverdata = res.data.data?.tab_list;
        // console.log(res.data.data?.tab_list);
      });
  },
  methods: {
      ...mapMutations(["goPage"]),
      // goPage:function (v) {
      //   console.log(v);
      // },
    taglist:function (v) {
      console.log(v);
      this.$router.push({path:'/showgoods',query:{id:v}})
    },
    min_dianzan:function (event) {
         if (this.goodjob.includes(event)) {
      this.goodjob=this.goodjob.filter((o)=>{
        return !(o==event)
      })
    }else{
      this.goodjob=[...this.goodjob,event]
    }
    // console.log(this.goodjob ,event);
    localStorage.setItem("goodjob",JSON.stringify(this.goodjob))
      
    },
        viewimg(i,e) {
          let imgs=[]
          
          e.forEach(o=>{
           imgs.push(o.img) 
          })
      ImagePreview({
        images: imgs,
        showIndex: true,
        loop: false,
        startPosition: i
      })
        },
    dianzan:function (event) {
    
    if (this.arrdianz.includes(event)) {
      this.arrdianz=this.arrdianz.filter((o)=>{
        return !(o==event)
      })
    }else{
      this.arrdianz=[...this.arrdianz,event]
    }
    localStorage.setItem("arrdianz",JSON.stringify(this.arrdianz))
    },
    onLoad() {
      // 异步更新数据
      // setTimeout 仅做示例，真实场景中一般为 ajax 请求

      this.axios
        .get(
          "https://apis.netstart.cn/xmsc/communicate/home_rec?",
         { params:{
            size:10,
            after:this.after+10
          },}
        )
        .then((res) => {
          this.tuijiandata = [...this.tuijiandata, ...res.data.data.rec_list];
          // console.log(res.data.data.rec_list);
          // console.log(this.tuijiandata);
          //   // 加载状态结束
          this.$nextTick(() => {
            this.loading = false;
            if (this.tuijiandata.length >=50) {
              this.finished = true;
            }
            this.after += 10;
          });
        });
    },
    showComment: function (id,con) {
      // console.log(id);
      this.cun=con
      // if (id.content_type== 3) {
      //    this.get("https://apis.netstart.cn/xmsc/communicate/comment_display_list?",{
      //   params:{
      //     content_id:,
      //     is_feature:false,
      //     size:10,
      //     page:1,
      //     is_summary:false
      //   }
      // })
      // }
     
      this.axios
        .get(
          "https://apis.netstart.cn/xmsc/communicate/question_answer_list?",
          {
            params: {
              q_id: id,
            },
          }
        )
        .then((res) => {
          console.log(res.data.data.answer_list);
          // console.log(res);
          if (res) {
            this.answer_list = res?.data?.data?.answer_list;
            this.show = true;
          }
        });
    },
  },
};
</script>

<style lang="less" >

