       border-radius: 50%;
       padding-top: 10rem;
display: flex;

 .van-image{
     width: 30rem;
      height: 30rem;

 }
   
    }

    .fl {
   width: 100%;
      .nickname_top {
        color: rgb(172, 168, 168);
