  margin-bottom: 0rem;
  .ulul {
    // margin: 15rem;
    margin-top: 30rem;
    .lili {
      a {
        img {
          border-radius: 10rem;
        }
      }
      .question {
        border-bottom: 1rem solid rgba(169, 168, 168, 0.825);
        text-align: left;
        p {
