import Vue from 'vue'
import router from '@/router'
import Vuex from 'vuex'
// import axios from "axios";
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    // pagedata: null,
    showpath: '',
    showId: ''
  },
  getters: {
  },
  mutations: {
    goPage(state, content) {
      state.showId = content.id
      switch (content.content_type) {
        case 1:
          router.push({ path: "content", query: { id: content.id } });
          break;
        case 3:
          router.push({ path: "article", query: { id: content.id } });
          break;
        case 5:
          router.push({ path: "vote", query: { id: content.id } });
          break;
        case 6:
          router.push({ path: "question", query: { id: content.question.q_id } });
          break;
        case 99:
          router.push({ path: "moment", query: { id: content.id } });
          break;
        case 100:
          router.push({ path: "product", query: { id: content.id } });
          break;
        default:
          break;
      }
    },
    goToSomePage(state, action) {
      // console.log(action);
      state.showpath = action
      switch (action.type) {
        case "product":
          // console.log(router,this);
          router.push({ path: "showgoods", query: { id: action.path } });
          break;
        case "first_channel":
          router.push({
            path: "actiongoods",
            query: { id: action.path },
          });
          // console.log(action);
          break;
        case "activity":
          router.push({
            path: "activity",
            query: { id: action.path },
          });
          break;
        case "cate":
          // console.log('是cate');
          router.push({
            path: "/allproduct",
            query: { id: action.path },
          });

          break;
        case "second_channel":
          console.log("second_channel");
          break;
        case "keyword":
          console.log("keyword");
          break;
        case "url":
          console.log("url");
          break;
        default:
          break;
      }
    },
  },
  actions: {

  },
  modules: {
  }
})
