<template>
  <section class="sy" v-if="dianshi">
    <dir class="loading">
      <van-loading type="spinner" v-if="!dianshi" size="46" color="red" />
    </dir>
    <div v-if="dianshi">
      <BigPic :homeTJ="dianshi.sections[0]?.body?.items[0]" />
      <div style="height: 8rem; background-color: rgb(255, 174, 153)"></div>
      <ListThreeLi
        :homeTJ="dianshi.sections[2]?.body?.items"
        style="background-color: rgb(255, 174, 153)"
      />
      <BigPic :homeTJ="dianshi.sections[4]?.body?.items[0]" />
      <ListTwo :homeTJ="dianshi.sections[6]?.body.items" />
      <ListThreeLi
        :homeTJ="dianshi.sections[8]?.body.items"
        style="background-color: rgb(255, 174, 153)"
      />
      <div style="height: 8rem; background-color: rgb(255, 174, 153)"></div>
      <BigPic :homeTJ="dianshi.sections[10]?.body.items[0]" />
      <div style="height: 8rem; background-color: rgb(255, 174, 153)"></div>
      <BigPic :homeTJ="dianshi.sections[12]?.body.items[0]" />
      <ListTwo :homeTJ="dianshi.sections[14]?.body.items" />
      <BigPic :homeTJ="dianshi.sections[16]?.body.items[0]" />
      <ListTwo :homeTJ="dianshi.sections[18]?.body.items" />
      <ListThreeLi
        :homeTJ="dianshi.sections[20]?.body.items"
        style="background-color: rgb(255, 174, 153)"
      />
      <ListThreeLi
        :homeTJ="dianshi.sections[22]?.body.items"
        style="background-color: rgb(255, 174, 153)"
      />
      <BigPic :homeTJ="dianshi.sections[24]?.body.items[0]" />
    </div>
     <div class="tupian_top"   v-if="showTop" >
      <img src="@/assets/top.png" @click="sllTop" id="btnBox" alt="">
     </div>
  </section>
</template>
 
<script>
import BigPic from "@/components/homeview/tool/BigPic.vue";
import ListThreeLi from "@/components/homeview/tool/ListThreeLi.vue";
import ListTwo from "@/components/homeview/tool/ListTwo.vue";

export default {
  data: function () {
    return {
      dianshi: null,
       showTop:false,
    };
  },
  components: {
    BigPic,
    ListThreeLi,
    ListTwo,
  },
  created() {
     
    this.axios.get("https://apis.netstart.cn/xmsc/home/page", {
     params: { page_type: "activity", page_id: 19071 },
      }).then((res) => {
        this.dianshi = res.data.data.data;
      });

    // axios
    //   .get("dianshi.json")
    //   .then((res) => {
    //     this.dianshi = res.data.data.data;
    //   });
  },
    mounted(){
  window.addEventListener("scroll",this.scrollToTop)
  },
    destroyed(){
  window.removeEventListener("scroll",this.scrollToTop)
  },
  methods: {
    sllTop:function () {
      document.documentElement.scrollTop=0
    },
  scrollToTop:function () {
    // let topBtn=document.getElementById("btnBox")
    var scrollTop=window.pageYOffset||document.documentElement.scrollTop||document.body.scrollTop;
    // console.log(scrollTop);
    if (scrollTop>500) {
      this.showTop=true
    }else if (scrollTop<500){
      this.showTop=false
    }
  },
  }
};
</script>

<style lang="less" scoped>
.loading {
  position: absolute;
  margin-top: 200rem;
  left: 35%;
}
   .tupian_top{
  position: fixed;
  bottom: 70rem;
  right: 20rem;
  z-index: 99;
  img{
    width: 40rem;
  }
}
</style>