<template>
  <section class="sy" >
       <dir class="loading">
      <van-loading type="spinner" v-if="!jiadian" size="46" color="red"/>
    </dir>
    <div v-if="jiadian">
      <BigPic :homeTJ="jiadian?.data?.sections[0].body.items[0]" />
    <BigPic :homeTJ="jiadian?.data?.sections[2].body.items[0]" />
    <div style="height: 8rem; background-color: rgb(225, 65, 57)"></div>
    <ListThreeLi
      :homeTJ="jiadian?.data?.sections[4]?.body.items"
      style="background-color: rgb(225, 65, 57)"
    />
    <BigPic :homeTJ="jiadian?.data?.sections[6].body.items[0]" />
    <div style="height: 8rem; background-color: rgb(225, 65, 57)"></div>

    <ListThreeLi
      :homeTJ="jiadian?.data?.sections[8]?.body.items"
      style="background-color: rgb(225, 65, 57)"
    />
    <ListThreeLi
      :homeTJ="jiadian?.data?.sections[10]?.body.items"
      style="background-color: rgb(225, 65, 57)"
    />

    <BigPic :homeTJ="jiadian?.data?.sections[11].body.items[0]" />
    <BigPic :homeTJ="jiadian?.data?.sections[13].body.items[0]" />
    <div style="height: 8rem; background-color: rgb(225, 65, 57)"></div>
    <BigPic :homeTJ="jiadian?.data?.sections[15].body.items[0]" />
    <div style="height: 8rem; background-color: rgb(225, 65, 57)"></div>
    <ListThreeLi
      :homeTJ="jiadian?.data?.sections[16]?.body.items"
      style="background-color: rgb(225, 65, 57)"
    />
    <ListThreeLi
      :homeTJ="jiadian?.data?.sections[18]?.body.items"
      style="background-color: rgb(225, 65, 57)"
    />
    <ListThreeLi
      :homeTJ="jiadian?.data?.sections[20]?.body.items"
      style="background-color: rgb(225, 65, 57)"
    />
    <div style="height: 60rem; background-color: rgb(225, 65, 57)"></div>
    </div>
     <div class="tupian_top"   v-if="showTop" >
      <img src="@/assets/top.png" @click="sllTop" id="btnBox" alt="">
     </div>
  </section>
</template>

<script>
import BigPic from "@/components/homeview/tool/BigPic.vue";
import ListThreeLi from "@/components/homeview/tool/ListThreeLi.vue";
export default {
  data: function () {
    return {
      jiadian: null,
        showTop:false,
    };
  },
  components: {
    BigPic,
    ListThreeLi,
  },
  created() {
    this.axios
      .get("jiadian.json")
      .then((res) => {
        this.jiadian = res.data.data;
      });
  },
   mounted(){
  window.addEventListener("scroll",this.scrollToTop)
  },
    destroyed(){
  window.removeEventListener("scroll",this.scrollToTop)
  },
  methods: {
    sllTop:function () {
      document.documentElement.scrollTop=0
    },
  scrollToTop:function () {
    // let topBtn=document.getElementById("btnBox")
    var scrollTop=window.pageYOffset||document.documentElement.scrollTop||document.body.scrollTop;
    // console.log(scrollTop);
    if (scrollTop>500) {
      this.showTop=true
    }else if (scrollTop<500){
      this.showTop=false
    }
  },
  }
};
</script>

<style lang="less" scoped>
 .loading {
position: absolute;
margin-top: 200rem;
left: 35%;
  }
   .tupian_top{
  position: fixed;
  bottom: 70rem;
  right: 20rem;
  z-index: 99;
  img{
    width: 40rem;
  }
}
</style>