<template>
  <section>
    <div class="loading">
      <van-loading type="spinner" v-if="!detail" size="46" color="red" />
    </div>
    <div v-if="detail">
      <header class="channel_info">
        <div class="chann-top">
          <van-icon
            name="arrow-left"
            color="#f2f2f2"
            size="26rem"
            @click="$router.back()"
          />
          <van-icon
            name="search"
            color="#f2f2f2"
            size="26rem"
            @click="$router.push('/search')"
          />
        </div>
        <van-swipe :loop="true" :autoplay="3000">
          <van-swipe-item @click="goToSomePage(item.action)">
            <img :src="detail?.sections[0]?.body.items[0].img_url" alt="" />
          </van-swipe-item>
          <van-swipe-item @click="goToSomePage(item.action)">
            <img :src="detail?.sections[0]?.body.items[0].img_url" alt="" />
          </van-swipe-item>
        </van-swipe>
      </header>
      <section class="chann-mb">
        <section class="yi_kuai">
          <div class="next_items">
            <div
              class="next_item"
              v-for="(item, index) in detail?.sections[1]?.body.next_items"
              :key="index"
            >
              <img :src="item.img_url" alt="" />
              <div class="title">{{ item.title }}</div>
            </div>
          </div>
          <div class="next_hs">
            <div
              class="next_h"
              v-for="(item, index) in detail?.sections[1]?.body.items"
              :key="index"
            >
              <img :src="item.img_url" alt="" />
              <div class="title">{{ item.title }}</div>
            </div>
          </div>
        </section>
      </section>
      <section class="liang_kuai">
        <div class="default_line">
          <div
            v-for="(item, index) in detail?.sections[3]?.body.items"
            :key="index"
          >
            <img :src="item.img_url" alt="" />
          </div>
        </div>
        <section class="si_kuai">
          <p class="si-title">{{ detail?.sections[5].body.title }}</p>
          <ul>
            <li
              v-for="(item, index) in detail?.sections[6]?.body.items"
              :key="index"
            >
              <div class="product_name">{{ item.product_name }}</div>
              <div class="product_tag">{{ item.product_tag }}</div>
              <div class="product_price">￥{{ item.product_price }}</div>
              <div class="button_text">{{ item.button_text }}</div>
              <div class="img_url">
                <img :src="item.img_url" alt="" />
              </div>
            </li>
          </ul>
        </section>
      </section>
    </div>
  </section>
</template>

<script>
export default {
  data: function () {
    return {
      detail: null,
    };
  },

  methods: {
    getDetail(id) {
      this.axios
        .get("https://apis.netstart.cn/xmsc/home/channel1_info?", {
          params: { page_id: id },
        })
        .then((res) => {
          this.detail = res.data.data;
          // console.log(res, this.detail?.sections[0]?.body);
        });
    },
  },
  created() {
    this.getDetail(this.$route.query.id);
  },
  beforeRouteUpdate(to) {
    this.getDetail(to.query.id);
  },
};
</script>

<style lang="less" scoped>
.loading {
  position: absolute;
  // top: 200rem;
  margin-top: 200rem;
  left: 45%;
}
.channel_info {
  position: relative;
  .chann-top {
    position: absolute;
    z-index: 9;
    display: flex;
    justify-content: space-between;
    align-items: center;
    top: 0;
    left: 0;
    width: 100%;
    padding: 10rem;
  }
}
.chann-mb {
  position: absolute;
  top: 200rem;
  width: 100%;
  .yi_kuai {
    background-color: #fff;
    border-radius: 10rem;
    width: 350rem;
    margin: 0 auto;
    .next_hs {
      display: flex;
      justify-content: space-around;
      padding-bottom: 10rem;
      .next_h {
        img {
          width: 50rem;
        }
        .title {
          color: rgb(75, 74, 74);
          padding: 10rem 0;
          font-size: 13rem;
        }
      }
    }
    .next_items {
      display: flex;
      justify-content: space-around;
      align-content: center;
      padding: 10rem 0;
      .next_item {
        display: flex;
        justify-content: center;
        align-items: center;
        img {
          width: 18rem;
          margin-right: 8rem;
        }
        .title {
          color: rgb(81, 80, 80);
          font-size: 12rem;
        }
      }
    }
  }
}
.liang_kuai {
  background-color: rgba(218, 214, 214, 0.671);
  padding: 0 14rem;
  .default_line {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 30rem 0;
  }
  .si_kuai {
    p.si-title {
      text-align: left;
      font-size: 18rem;
      font-weight: 600;
      margin-bottom: 20rem;
    }
    ul {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      li {
        width: 49.8%;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        padding: 10rem;
        background-color: #fff;
        position: relative;
        .product_name {
          width: 100%;
          color: rgb(27, 26, 26);
          font-size: 16rem;
          font-weight: 600;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          text-align: left;
        }
        .product_tag {
          width: 78%;
          color: rgb(66, 64, 64);
          font-size: 13rem;
          position: relative;
          z-index: 9;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
        .product_price {
          font-size: 14rem;
          color: rgb(52, 51, 51);
          padding-top: 8rem;
          margin-left: -2rem;
        }
        .button_text {
          background-color: #ec6055;
          color: #fff;
          margin: 4rem;
          font-size: 12rem;
          padding: 0 2rem;
          border-radius: 2rem;
        }
        .img_url {
          position: absolute;
          bottom: 6rem;
          right: 6rem;
          width: 68rem;
          z-index: 8;
          img {
            width: 100%;
            height: 100%;
          }
        }
      }
    }
  }
}
</style>