<template>
  <div>
    <div class="top_ad">
      <van-icon
        name="arrow-left"
        size="24rem"
        color="#8c8989"
        @click="$router.back()"
      />
      <div class="arrow-left">内容详情</div>
    </div>
    <van-loading type="spinner" v-if="!detail" />
    <div class="buy_xiu">
      <div class="hot_entity" v-if="detail?.data?.entity_circle">
        <div class="hot_left">
          <van-image :src="detail?.data?.entity_circle?.img" class="img_p">
            <template v-slot:loading>
              <van-loading type="spinner" size="20" />
            </template>
          </van-image>
          <div class="circle_name">{{ detail?.data?.entity_circle?.name }}</div>
        </div>
        <div class="hot_rigth">
          {{ detail?.data?.entity_circle?.hot }}热度
          <van-icon name="arrow" />
        </div>
      </div>
      <div class="user_t">
        <div class="user_top">
          <div class="user_left">
            <div class="img_p">
              <img :src="detail?.data?.icon" alt="" />
            </div>
            <div class="n_t">
              <div class="name_u">{{ detail?.data?.nickname }}</div>
              <div class="create_time">{{ detail?.data?.create_time }}</div>
            </div>
          </div>
          <div class="user_rigth">关注</div>
        </div>
        <div class="content">
          <div class="con_title">{{ detail?.data?.content }}</div>
          <div class="con_img">
            <div
              v-for="(img, index) in detail?.data?.pic_list"
              :key="index"
              class="imag"
              @click.stop="viewimg(index, detail?.data?.pic_list)"
            >
              <van-image
                :src="img.img"
                fit="cover"
                height="100%"
                width="100%"
                class="img"
              >
                <template v-slot:loading>
                  <van-loading type="spinner" size="20" />
                </template>
              </van-image>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="footer">
      <div class="buy_p" @click="$router.push({path:'/showgoods',query:{id:detail?.data?.products[0]?.product_id},})">￥{{detail?.data?.products[0]?.price}}起 立即购买<van-icon name="arrow" /></div>
      <img src="@/assets/share.png" alt="" />
      <div>
        <img
          src="@/assets/msgf.png"
          alt=""
         
        /><span  v-text="detail?.data?.comment_num ? detail?.data?.comment_num : ''"></span>
      </div>
      <div>
        <van-icon
          name="good-job-o"
          size="18rem"
          :color="arrdianzs.includes(detail?.data?.id) ? 'red' : ''"
          @click="dianzans(detail?.data?.id)"
        />
        <span
          v-text="
            arrdianzs.includes(detail?.data?.praise_num)?detail?.data?.praise_num*1+1:detail?.data?.praise_num"
        >
        </span>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import { ImagePreview } from "vant";
Vue.use(ImagePreview);
export default {
  data: function () {
    return {
      detail: [],
      arrdianzs: JSON.parse(localStorage.getItem("arrdianzs")) || [],
    };
  },
  created() {
    this.getDetail(this.$route.query.id);
  },
  beforeRouteUpdate(to) {
    this.getDetail(to.query.id);
  },
  methods: {
     viewimg(i, e) {
      let imgs = [];

      e.forEach((o) => {
        imgs.push(o.img);
      });
      ImagePreview({
        images: imgs,
        showIndex: true,
        loop: false,
        startPosition: i,
      });
    },
    dianzans: function (event) {
      console.log(event);
      if (this.arrdianzs.includes(event)) {
        this.arrdianzs = this.arrdianzs.filter((o) => {
          return !(o == event);
        });
      } else {
        this.arrdianzs = [...this.arrdianzs, event];
      }
      localStorage.setItem("arrdianzs", JSON.stringify(this.arrdianzs));
    },
    getDetail(id) {
      this.axios
        .get("https://apis.netstart.cn/xmsc/communicate/content_detail?", {
          params: { content_id: id },
        })
        .then((res) => {
          this.detail = res.data;
          console.log(res.data);
        });
    },
  },
};
</script>

<style lang="less" scoped>
.footer {
  display: flex;
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 50rem;
  background-color: rgb(245, 239, 239);
  align-items: center;
  justify-content: space-around;
  .buy_p{
    padding: 8rem 6rem;
    justify-content: center;
    background: #ff5528;
    color: #fff;
    text-align: center;
    border-radius: 20rem;
    font-size: 13rem;
  }
  img {
    width: 20rem;
  }
  div {
    display: flex;
    align-items: center;
    color: #757575;
  span{
      font-size: 13rem;
    padding-left: 3rem;
  }
  }
}
.user_t {
  padding: 12rem;
  .content {
    .con_title {
      text-align: left;
      color: #333;
      font-size: 16rem;
      margin: 12rem 0;
    }
    .con_img {
      display: flex;
      flex-wrap: wrap;
      width: 99%;
      align-items: center;
      justify-content: space-between;

      .imag {
        width: 115rem;
        height: 115rem;
        border-radius: 8rem;
        overflow: hidden;
        object-fit: cover;

        .img {
          width: 115rem;
          height: 115rem;
          border-radius: 6rem;
        }
      }
    }
  }
  .user_top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    .user_left {
      display: flex;
      align-items: center;
      justify-content: left;
      .img_p {
        width: 30rem;
        height: 30rem;
        img {
          border-radius: 50%;
          width: 100%;
          height: 100%;
        }
      }
      .n_t {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: left;
        margin-left: 8rem;
        .name_u {
          text-align: left;
          font-weight: 500;
          color: #161616;
        }
        .create_time {
          font-weight: 500;
          color: #757575;
          font-size: 12rem;
        }
      }
    }
    .user_rigth {
      display: flex;
      align-items: center;
      justify-content: right;
      padding: 4rem 18rem;
      border: 1rem solid #ff5528;
      border-radius: 16rem;
      color: #ff5528;
    }
  }
}
.buy_xiu {
  .hot_entity {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1rem solid rgb(209, 206, 206);
    padding: 8rem;
    .hot_left {
      display: flex;
      align-items: center;
      justify-content: left;
      .img_p {
        width: 30rem;
        height: 30rem;
      }
      .circle_name {
        font-size: 13rem;
        margin-left: 6rem;
      }
    }
    .hot_rigth {
      display: flex;
      align-items: center;
      justify-content: right;
      color: #999;
      font-size: 13rem;
    }
  }
}
.top_ad {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: sticky;
  padding: 16rem 10rem;
  height: 60rem;
  top: 0;
  z-index: 0;
  background-color: #fff;
  .arrow-left {
    width: 100%;
    font-size: 16rem;
    color: #666;
  }
}
</style>