<template>
    <div>
            <div class="top_ad">
      <van-icon
        name="arrow-left"
        size="24rem"
        color="#8c8989"
        @click="$router.back()"
      />
      <div class="arrow-left">投票详情</div>
    </div>
<div class="description">{{detail?.description}}···</div>
<van-loading type="spinner" v-if="!detail"/>
    </div>
</template>

<script>
    export default {
        data:function () {
            return{
                detail:[]
            }
        },
        created() {
    this.getDetail(this.$route.query.id);
  },
  beforeRouteUpdate(to) {
    this.getDetail(to.query.id);
  },
        methods:{
            getDetail(id){
                this.axios.get("https://apis.netstart.cn/xmsc/communicate/vote_detail?",{
                    params:{vote_id:id},
                }).then(res=>{
                    this.detail=res.data
                    console.log(res.data);
                })
            }
        }
    }
</script>

<style lang="less" scoped>
.description{
    font-size: 16rem;
    padding: 20rem;
    color: #666;
}
.top_ad {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: sticky;
  padding: 16rem 10rem;
  height: 50rem;
  top: 0;
  z-index: 0;
  background-color: rgb(242, 242, 242);
  .arrow-left {
    width: 100%;
    font-size: 16rem;
    color: #666;
  }
}
</style>