<template>
  <div class="home" >
    <header>
      <section class="h-top1">
        <img src="@/assets/icon-header-logo3.png" alt="" />
        <router-link class="h-title" to="/search">
          <img src="@/assets/ipt-icon.png" alt="" />
          <span>搜索商品名称</span>
        </router-link>
        <img src="@/assets/icon-user.png" alt="" @click="$router.push({path:'/user'});$emit('change-home',4)"/>
      </section>
      <section class="h-top2">
        <span
          v-for="(item, index) in homeTJ?.tabs"
          :class="{ active: temp === index }"
          :key="index"
          @click="changitem(index,item)"
          >{{ item?.name }}</span
        >
      </section>
    </header>
    <section class="content">
      <transition-group name="toggle-show">
        <TuiJian v-if="temp === 0" :key="0" :homeTJ="homeTJ" />
        <ZhiNeng v-if="temp === 1" :key="1"/>
        <DianShi v-if="temp === 2" :key="2" />
        <JiaDian v-if="temp === 3" :key="3" />
        <BiJiBen v-if="temp === 4" :key="4" />
      </transition-group>
    </section>
  </div>
</template>
<script>
import TuiJian from "@/components/homeview/TuiJian.vue";
import ZhiNeng from "@/components/homeview/ZhiNeng.vue";
import DianShi from "@/components/homeview/DianShi.vue";
import JiaDian from "@/components/homeview/JiaDian.vue";
import BiJiBen from "@/components/homeview/BiJiBen.vue";
export default {
  name: "HomeView",
  components: {
    TuiJian,
    ZhiNeng,
    DianShi,
    JiaDian,
    BiJiBen,
  },
  data: function () {
    return {
      temp: 0,
      homeTJ: null,
    };
  },
  created() {
    this.axios.get("tuijian.json").then((res) => {
      this.homeTJ = res.data.data;
      // console.log(res);
    });
  },
  methods: {
    changitem: function (index) {
        this.temp = index;
      
    },
  },
};
</script>
