<template>
  <div>
    <div class="top_cart">
      <van-icon
        name="arrow-left"
        size="25rem"
        color="#777"
        @click="$router.back()"
      />
      <div style="color: 'rgb(102, 102, 102)'" class="arrow-left">商品评论</div>
    </div>
    <div class="comment_tags">
      <div
        class="comment_tag"
        v-for="(tag, index) in detail?.comments?.detail?.comment_tags"
        :key="index"
      >
        {{ tag.tag_name }}
      </div>
    </div>
    <div class="user_avatars" v-for="(item,index) in detail?.comments?.list" :key="index">
      <div class="user_avatar">
        <img :src="item.user_avatar" alt="" class="img_us" />
        <div class="user_er">
          <div class="user_name">
            {{ item.user_name }}
          </div>
          <div class="add_time">
            {{ item.add_time }}
          </div>
        </div>
      </div>
      <div class="comment_contents" v-html="item.comment_content"></div>
      <div class="comment_images">
        <div
          class="img"
          v-for="(img, inde) in item.comment_images"
          :key="inde"
          @click.stop="viewimg(inde, item.comment_images)"
        >
          <van-image :src="img" fit="cover" width="80rem" height="80rem">
            <template v-slot:loading>
              <van-loading type="spinner" size="20" />
            </template>
          </van-image>
        </div>
      </div>
      <div v-if="item.reply_content" class="user_replys">
        <div class="reply_content">
          <img src="@/assets/gf.png" alt="" />
          <span>官方回复：</span>
          {{ item.reply_content ? item.reply_content : "" }}
        </div>
      </div>
      <div v-if="item.user_replys.length !== 0">
        <div
          class="userreplys"
          v-for="(repl, index) in item.user_replys"
          :key="index"
        >
          <img :src="repl.user_avatar" alt="" />
          <div class="reply_content">
            {{ repl.user_name }}:
            <span class="sp">{{ repl.reply_content }}</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Vue from 'vue';
import { ImagePreview } from 'vant';
Vue.use(ImagePreview);
export default {
  data: function () {
    return {
      detail: [],
    };
  },
  created() {
    this.getDetail(this.$route.query.id);
  },
  
     methods:{
 viewimg(i,e) {
      ImagePreview({
        images: e,
        showIndex: true,
        loop: false,
        startPosition: i
      })
    },
       getDetail(id) {
      this.axios
        .get("https://apis.netstart.cn/xmsc/miproduct/view?", {
          params: { commodity_id: id },
        })
        .then((res) => {
          this.detail = res.data.data?.goods_share_datas;
          console.log(res.data.data.goods_share_datas);
        });
    },
  
        },
 
};
</script>

<style lang="less" scoped>
.user_replys{
    display: flex;
    align-items: center;
    color: #000;
    background-color: #f6f6f6;
    
    .reply_content{
line-height: 20rem;
text-align: left;
padding: 6rem;
      font-size: 12rem;
 img{
        width: 18rem;
        height: 18rem;
        display: inline-block;
        border-radius: 50%;
           position: relative;
           top:5rem;
      
    }
    span{
    padding: 10rem;
    color: #ff6700;
    }
    }
   
}
.userreplys{
    display: flex;
    align-items: center;
     background-color: #f6f6f6;
     padding: 8rem;
    img{
        width: 18rem;
        border-radius: 50%;
    }
    .reply_content{
        padding-left:0rem;
        font-size: 12rem;
        .sp{
              padding-left: 6rem; 
        }
    }
}
.comment_tags {
  padding: 10rem;
  display: flex;
  flex-wrap: wrap;
  .comment_tag {
    padding: 2rem 8rem;
    background-color: #ffeeea;
    border-radius: 10rem;
    margin-top: 4rem;
    margin-right: 6rem;
  }
}
 .top_cart {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: sticky;
  padding: 16rem 10rem;
  height: 50rem;
  top: 0;
  z-index: 99;
  background-color: rgb(245, 243, 243);
  .arrow-left {
    width: 100%;
    font-size: 16rem;
    color: #666;
  }
}
.user_avatars{
     padding: 16rem;
   width: 100%;
border-bottom: 1rem solid rgb(218, 215, 215);
.user_avatar{
    display: flex;
    align-items: center;
img{
    width: 40rem;
    border-radius: 50%;
  
}
.user_er{
        padding-left: 8rem;
        display: flex;
        flex-direction: column;
        align-items: left;
.user_name{
font-size: 13rem;
text-align: left;
}
.add_time{
    color: rgba(0,0,0,.54);
font-size: 12rem;
// padding-left: 4em;
padding-top: 4rem;

}
}

}
.comment_contents{
    text-align: left;
    padding: 8rem 0;
    font-size: 14rem;
}
.comment_images{
   padding: 8rem 0;
   display: flex;
     flex-wrap: wrap;
    .img{
        display: flex;
        padding-right:6rem ;
        padding-bottom: 6rem;
   
    }
}
}
</style>