<template>
  <section class="showdoogs">
    <div class="loading">
      <van-loading
        type="spinner"
        v-if="detail?.length==0"
        size="46"
        color="red"
      />
    </div>
    <div v-if="!(detail?.length==0)">
      <header class="h-top">
        <div class="h-vanicon1" @click="$router.back()">
          <img src="@/assets/icon-back3.png" alt="" />
        </div>
        <div class="h-vanicon2">
          <img
            src="@/assets/icon-share-white.png"
            @click="showShare = true"
            alt=""
          />

          <van-share-sheet
            v-model="showShare"
            title="分享"
            :options="options"
            @select="onSelect"
          />
        </div>
        <van-swipe
          class="my-swipe"
          :autoplay="3000"
          indicator-color="white"
          @change="onChange"
        >
          <van-swipe-item
            v-for="(icon, index) in detail?.goods_info[0]?.gallery_v3"
            :key="index"
            class=""
            ><van-image :src="icon.img_url?icon.img_url:''">
              <template v-slot:loading>
                <van-loading type="spinner" size="20" />
              </template>
            </van-image>
          </van-swipe-item>
          <template #indicator>
            <div class="custom-indicator">
              {{ current + 1 }} /
              {{ detail?.goods_info[0]?.gallery_v3?.length }}
            </div>
          </template>
        </van-swipe>
      </header>
      <section class="xiangqing">
        <div class="price">
          <p>
            ￥{{ detail?.goods_info[0]?.price }}
            <span
              v-if="
                !(
                  detail?.goods_info[0]?.price ===
                  detail?.goods_info[0]?.market_price
                )
              "
              >￥{{ detail?.goods_info[0]?.market_price }}</span
            >
          </p>
        </div>
        <div class="things_name">
          {{ detail?.product_info?.name }}
        </div>
        <div
          v-text="detail?.product_info?.product_desc_ext"
          class="desc_ext"
        ></div>
        <div class="sell_point">
          <div
            class="sell_point_desc"
            v-for="(item, index) in detail?.product_info?.sell_point_desc"
            :key="index"
          >
            <span class="one">{{ index + 1 }}</span>
            <div class="item_title">{{ item }}</div>
          </div>
        </div>
      </section>
      <div class="aboutData" v-if="aboutData">
        <van-tabs color="transparent">
          <van-tab v-for="(list, index) in aboutData" :key="index">
            <template #title>
              <div class="list_li">
                <img class="icon" :src="list?.icon?list?.icon:''" alt="" />
                <div class="top_title">{{ list?.top_title }}</div>
                <div class="bottom_title">
                  {{
                    list?.bottom_title.slice(0, list?.bottom_title.indexOf("+"))
                  }}
                </div>
              </div></template
            >
          </van-tab>
        </van-tabs>
      </div>
      <div class="list_new">
        <div class="ul_li">
          <div
            v-for="(list_new, index) in detail?.goods_info[0]?.service_refound_policy_list?.list_new"
            :key="index"
            class="list_s"
          >
            <van-icon name="passed" />
            <div class="title">{{ list_new.title }}</div>
          </div>
        </div>
        <div class="arrow_r">
          <van-icon name="arrow" @click="show = !show" />
        </div>
        <van-popup
          v-model="show"
          round
          position="bottom"
          closeable
          :style="{ height: '75%' }"
        >
          <div class="van-popup_ss">
            <div class="say_ser">服务说明</div>
            <div
              v-for="(list_new, index) in detail?.goods_info[0]?.service_refound_policy_list?.list_new"
              :key="index"
              class="van-popup_s"
            >
              <div class="img_s">
                <img src="@/assets/go.png" alt="" />
                <div class="title_s">{{ list_new.title }}</div>
              </div>
              <div
                v-html="list_new.desc.replace('\n', '<br/>')"
                class="desc_s"
                v-if="list_new.desc"
              ></div>
            </div>
          </div>
        </van-popup>
      </div>
      <div class="goods_share_datas">
        <div class="comments" v-if="detail?.goods_share_datas?.comments?.detail?.comment_tags">
          <p>用户评价</p>
          <div class="satisfy_per" @click="$router.push({path:'/goodspage',query:{id:goodsId},})">
            {{ detail?.goods_share_datas?.comments?.detail?.satisfy_per }}<van-icon name="arrow" class="arrow_w"/>
          </div>
        </div>
        <div class="comment_tags">
          <div
            class="comment_tag"
            v-for="(tag, index) in detail?.goods_share_datas?.comments?.detail?.comment_tags"
            :key="index"
          >
            {{ tag.tag_name }}
          </div>
        </div>
      </div>
      <CartText :detail="detail?.goods_share_datas?.comments?.list" />
      <div style="height: 12rem; background: rgb(246, 246, 246)"></div>
      <van-tabs color="transparent" title-active-color="#ff5934">
        <template v-for="(pagePic, index) in pageImg?.body?.items"
          ><van-tab :title="pagePic.tab_name" :key="index">
            <PageInfo :pagePic="pagePic" /></van-tab
        ></template>
      </van-tabs>
      <div class="tupian_top" v-if="showTop">
        <img src="@/assets/top.png" @click="sllTop" id="btnBox" alt="" />
      </div>
      <section class="maskk" ref="mask" v-show="showCartlist">
        <van-icon
          name="cross"
          class="cross"
          @click="showCartlist = !showCartlist"
        />
        <div class="mask_topp">
          <ul class="uu">
            <li class="ll">
              <div class="showli">
                <img :src="current_goods?.img_url?current_goods?.img_url:''" alt="" />
                <div class="price">
                  <p>
                    ￥{{ current_goods?.price }}
                    <span
                      v-if="
                        !(current_goods?.price == current_goods?.market_price)
                      "
                      >￥{{ current_goods?.market_price }}</span
                    >
                  </p>
                  <div class="name">{{ current_goods?.name }}</div>
                </div>
              </div>
            </li>
          </ul>
        </div>
        <div class="buy_option">
          <div
            class="buy-cart"
            v-for="(li, index) in detail?.buy_option"
            :key="index"
          >
            <div class="option_name">
              {{ li.name }}
            </div>
            <ul>
              <template v-for="(item, index2) in li?.list">
                <template v-if="index === 0">
                  <li :key="index2">
                    <p
                      @click="changeIn(li, item)"
                      :class="{
                        active:
                          item.prop_value_id ==
                          current_prop_list.find(
                            (o) => o.prop_cfg_id === li.prop_cfg_id
                          ).prop_value_id,
                      }"
                    >
                      {{ item.name }}
                    </p>
                  </li>
                </template>
                <template v-else>
                  <li
                    v-if="
                      current_left_prop[li.prop_cfg_id] &&
                      current_left_prop[li.prop_cfg_id].includes(
                        item.prop_value_id
                      )
                    "
                    :key="index2"
                  >
                    <p
                      @click="changeIn(li, item)"
                      :class="{
                        active:
                          item.prop_value_id ==
                          current_prop_list.find(
                            (o) => o.prop_cfg_id === li.prop_cfg_id
                          ).prop_value_id,
                      }"
                    >
                      {{ item.name }}
                    </p>
                  </li>
                </template>
              </template>
            </ul>
          </div>
          <div class="buy-cunt">
            <div class="option_name">购买数量</div>
            <div class="buy_fl">
              <van-stepper v-model="coun" />
            </div>
          </div>
        </div>
        <div class="decision" @click="decisionBuy(current_goods, coun)">
          确定
        </div>
      </section>
      <van-goods-action>
        <div class="icon-u" @click="toHome">
          <img src="@/assets/icon-home2.png" alt="" />
          <p>首页</p>
        </div>
        <div class="icon-u">
          <img src="@/assets/icon-service.png" alt="" />
          <p>客服</p>
        </div>
        <van-goods-action-icon
          icon="cart-o"
          text="购物车"
          :badge="sun"
          @click="toCart"
        />
        <van-goods-action-button
          type="warning"
          text="加入购物车"
          @click="addCart"
        />
        <van-goods-action-button type="danger" text="立即购买" />
      </van-goods-action>
    </div>
  </section>
</template>

<script>
import { Toast } from "vant";
import CartText from "@/components/CartText.vue";
import PageInfo from "@/components/PageInfo.vue";
export default {
  components: {
    CartText,
    PageInfo,
  },
  props: {
    sun: [String, Number],
  },
  data: function () {
    return {
      current: null,
      showTop: false,
      showCartlist: false,
      activeInd: null,
      activeIn: 0,
      pageImg: [],
      detail: [],
      coun: 1,
      show: false,
      showShare: false,
      goodsId:null,
      options: [
        { name: "微博", icon: "weibo" },
        { name: "复制链接", icon: "link" },
        //  window.location.href
      ],
      // addCartlist: JSON.parse(localStorage.getItem("addCartlist"))||[],

      // options:{},
      current_prop_list: null,
      current_goods: null,
      aboutData: [],
    };
  },
  watch: {
    detail: function (detail) {
      if (detail) {
        this.current_goods = detail.goods_info.find(
          (o) => o.goods_id == detail.default_goods_id
        );
        this.current_prop_list = this.current_goods.prop_list;
      } else {
        this.current_prop_list = null;
      }
    },
  },
  computed: {
    current_left_prop: function () {
      let xxx = {};
      let left = this.detail?.goods_info.filter(
        (o) =>
          o.prop_list[0].prop_value_id ===
          this.current_prop_list[0]?.prop_value_id
      );
      left.forEach((o) => {
        if (!xxx[o.prop_list[1].prop_cfg_id]) {
          xxx[o.prop_list[1].prop_cfg_id] = [];
        }
        xxx[o.prop_list[1].prop_cfg_id] = [
          ...xxx[o.prop_list[1].prop_cfg_id],
          o.prop_list[1].prop_value_id,
        ];
      });
      return xxx;
    },
  },
  mounted() {
    window.addEventListener("scroll", this.scrollToTop);
  },
  destroyed() {
    window.removeEventListener("scroll", this.scrollToTop);
  },
  methods: {
    sllTop: function () {
      document.documentElement.scrollTop = 0;
    },
    scrollToTop: function () {
      // let topBtn=document.getElementById("btnBox")
      var scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      // console.log(scrollTop);
      if (scrollTop > 500) {
        this.showTop = true;
      } else if (scrollTop < 500) {
        this.showTop = false;
      }
    },

    onSelect(option) {
      // Toast(option.name);
      if (option.icon == "link") {
        //  window.location.href
        // console.log( window.location.href);
        navigator.clipboard.writeText(window.location.href);
        Toast.success("已复制");
      }
      this.showShare = false;
    },
    getDetail(id) {
      this.axios
        .get("https://apis.netstart.cn/xmsc/miproduct/view?", {
          params: { commodity_id: id },
        })
        .then((res) => {
          this.detail = res.data.data;
          console.log(res.data.data);
         if (!(res.data.data)) {
          Toast.success(res.data.description);
         }
         
          res?.data?.data?.goods_tpl_datas[
            res?.data?.data?.product_info?.page_id
          ]?.sections.map((o) => {
            if (o.stat == "product_info_tab") {
              this.pageImg = o;
            }
          });
          this.aboutData =
            res?.data?.data?.goods_info[0]?.class_parameters?.list?.filter(
              (o) => o.is_page_show
            );
        });
    },

    toHome: function () {
      this.$router.push({ path: "/" });
      this.$emit("change-home", 0);
    },
    toCart: function () {
      this.$router.push({ path: "/cart" });
      this.$emit("change-home", 3);
    },
    decisionBuy: function (addbuy, coun) {
      // this.addCartlist =[{goods:addbuy,coun:coun},...this.addCartlist]
      Toast.success("加入成功");
      // console.log(addbuy);
      this.$emit("change-cart", { addbuy, coun });
    },
    // deleteCoun: function () {
    //   if (this.coun >= 1) {
    //     this.coun = this.coun - 1;
    //   }
    // },
    // addCoun: function () {
    //   this.coun = this.coun + 1;
    // },
    onChange: function (index) {
      this.current = index;
    },
    changeIn: function ({ prop_cfg_id }, select) {
      // console.log({ prop_cfg_id }, select);
      this.current_prop_list = this.current_prop_list.map((o) => {
        if (o.prop_cfg_id === prop_cfg_id) {
          return { ...o, prop_value_id: select.prop_value_id };
        } else {
          return o;
        }
      });
      if (!(this.detail?.length == 0)) {
        this.detail?.goods_info?.filter((o) => {
          if (
            JSON.stringify(o.prop_list) ==
            JSON.stringify(this.current_prop_list)
          ) {
            this.current_goods = o;
          }
        });
      }

      // console.log(this.current_goods);
    },
    addCart() {
      this.showCartlist = true;
    },
  },

  created() {
    this.getDetail(this.$route.query.id);
    this.goodsId=this.$route.query.id
  },
  beforeRouteUpdate(to) {
    this.getDetail(to.query.id);
     this.goodsId=to.query.id
  },
};
</script>

